import React from 'react';
import { Link } from 'react-router-dom';

import './evento-card-orcamento.css';

function EventoCardOrcamento({ id, codigo, cliente, valor, dia, usuario }) {
    let split = dia.split('-');
    let novadata = split[2] + "/" + split[1] + "/" + split[0];

    return (
        <div className="card-body card text-center">
            <div class="card-header">
                <h5>ID : {codigo}</h5>
            </div>
            <div class="card-body">
                <h5 className="card-text card-title text-center">Cliente : {cliente}</h5>
                <Link to={'/orcamentodetalhes/' + id} className="btn btnfixed2 btn-sm btn-detalhes">+ detalhes</Link>
            </div>
            <div class="card-footer text-muted">
                <p className="card-text"> Data: {novadata}</p>
            </div>
        </div>
    )
}

export default EventoCardOrcamento;