import React, { useState, useEffect } from 'react';
import './maquina.css';
import Navbar from '../navbar';
import Footer from '../footer';
import { useSelector } from 'react-redux';
import firebase from '../../config/firebase';
import EventoCardMaquina from '../evento-card-maquina'

function VisualizarEventosMaquinas({ match }) {

    const [maquinas, setMaquinas] = useState([]);
    const [pesquisa, setPesquisa] = useState('');
    const usuarioEmail = useSelector(state => state.usuarioEmail);
    const [carregando, setCarregando] = useState(1)
    let ListaMaquinas = [];

    useEffect(() => {

        if (match.params.parametro) {
            firebase.firestore().collection('maquinas').where('usuarioEmail', '==', usuarioEmail).get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    if (doc.data().nome.indexOf(pesquisa) >= 0) {
                        ListaMaquinas.push({
                            id: doc.id,
                            ...doc.data()
                        })
                    }
                })

                setMaquinas(ListaMaquinas);
                setCarregando(0)
            });

        } else {
            firebase.firestore().collection('maquinas').get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    if (doc.data().nome.indexOf(pesquisa) >= 0) {
                        ListaMaquinas.push({
                            id: doc.id,
                            ...doc.data()
                        })
                    }
                })

                setMaquinas(ListaMaquinas);
                setCarregando(0)
            });
        }
    }, [ListaMaquinas, match.params.parametro, pesquisa, usuarioEmail]);

    return (
        <>
            <Navbar />
            {
                carregando ? <div className="row mt-5"><div class="spinner-border text-danger mx-auto" role="status"></div></div>
                    :
                    <div className="body-content col-sm-12 col-md-8 mx-auto datascreen">
                        <div className="row p-3 mx-auto">
                            <input onChange={(e) => setPesquisa(e.target.value)} type="text"
                                className="form-control mx-auto search col-sm-8 text-center"
                                placeholder="Pesquisar Maquina pelo nome..." />
                        </div>
                        <div className="row exibicao mx-2 text-center">
                            {maquinas.map(item => <EventoCardMaquina key={item.id} id={item.id}
                                nome={item.nome} area={item.area} marca={item.marca}
                                detalhes={item.detalhes} usuario={item.usuarioEmail} />)}
                        </div>
                    </div>
            }
            <Footer />
        </>
    )
}

export default VisualizarEventosMaquinas;