import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import './qualidade-cadastro.css';
import Navbar from '../../components/navbar/';
import Footer from '../../components/footer/';
import Emoji from 'a11y-react-emoji';

import firebase from '../../config/firebase';

function QualidadeCadastro(props) {

    const [carregando, setCarregando] = useState();
    const [msgTipo, setMsgTipo] = useState();
    const [titulo, setTitulo] = useState();
    const [numeroOf, setNumeroOf] = useState();
    const [codDesenho, setCodDesenho] = useState();
    const [quantOf, setQuantOf] = useState();
    const [quantNaoConforme, setQuantNaoConf] = useState();
    const [setorResponsavel, setSetorResponsavel] = useState();
    const [causaNCF, setCausaNCF] = useState();
    const [detalhes, setDetalhes] = useState();
    const [solucao, setSolucao] = useState();
    const [data, setData] = useState();
    const [hora, setHora] = useState();
    const [acesso, setAcesso] = useState('');
    const [setores, setSetores] = useState([]);
    const [tipo_ncf, set_tipo_ncf] = useState([]);

    const [foto1, setFoto1] = useState('');
    const [foto2, setFoto2] = useState('');
    const [foto3, setFoto3] = useState('');
    const [foto4, setFoto4] = useState('');
    const [foto5, setFoto5] = useState('');
    const usuarioEmail = useSelector(state => state.usuarioEmail);

    const storage = firebase.storage();
    const db = firebase.firestore();
    let ListaSetores = [];
    let ListaTiposNcf = [];

    useEffect(() => {
        if (props.match.params.id) {
            firebase.firestore().collection('qualidade').doc(props.match.params.id).get().then(resultado => {
                setHora(resultado.data().hora)
                setNumeroOf(resultado.data().numeroOf)
                setQuantNaoConf(resultado.data().quantNaoConforme)
                setSetorResponsavel(resultado.data().setorResponsavel)
                setSolucao(resultado.data().solucao)
                setTitulo(resultado.data().titulo)
                setCodDesenho(resultado.data().codDesenho)
                setDetalhes(resultado.data().detalhes)
                setData(resultado.data().data)
                setQuantOf(resultado.data().quantOf)
            })
        }
    }, [carregando, props.match.params.id])

    function atualizar() {
        setMsgTipo(null);
        setCarregando(1);

        db.collection('qualidade').doc(props.match.params.id).update({
            titulo: titulo,
            numeroOf: numeroOf,
            codDesenho: codDesenho,
            quantOf: quantOf,
            quantNaoConforme: quantNaoConforme,
            setorResponsavel: setorResponsavel,
            detalhes: detalhes,
            data: data,
            hora: hora,
            solucao: solucao
        }).then(() => {
            setMsgTipo('sucesso');
            setCarregando(0);
        }).catch(erro => {
            setMsgTipo('erro');
            setCarregando(0);
        });
    }

    function cadastrar() {
        setMsgTipo(null);
        setCarregando(1);

        if (foto1) { storage.ref(`qualidade/${foto1.name}`).put(foto1) }
        if (foto2) { storage.ref(`qualidade/${foto2.name}`).put(foto2) }
        if (foto3) { storage.ref(`qualidade/${foto3.name}`).put(foto3) }
        if (foto4) { storage.ref(`qualidade/${foto4.name}`).put(foto4) }
        if (foto5) { storage.ref(`qualidade/${foto5.name}`).put(foto5) }

        setTimeout(function(){
        db.collection('qualidade').add({
            titulo: titulo,
            causa: causaNCF,
            numeroOf: numeroOf,
            codDesenho: codDesenho,
            quantOf: quantOf,
            quantNaoConforme: quantNaoConforme,
            setorResponsavel: setorResponsavel,
            detalhes: detalhes,
            data: data,
            hora: hora,
            usuario: usuarioEmail,
            visualizacoes: 0,
            foto1: foto1 ? foto1.name : '',
            foto2: foto2 ? foto2.name : '',
            foto3: foto3 ? foto3.name : '',
            foto4: foto4 ? foto4.name : '',
            foto5: foto5 ? foto5.name : '',
            solucao: solucao,
            publico: 1,
            criacao: new Date()
        }).then(() => {
            setMsgTipo('sucesso');
            setCarregando(0);
            window.location.reload();
        }).catch(erro => {
            setMsgTipo('erro');
            setCarregando(0);
        });
    }, 15000);
    }

    useEffect(() => {
        let usersRef = db.collection('users');
        let query = usersRef.where('usuarioEmail', '==', usuarioEmail).get()
            .then(snapshot => {
                if (snapshot.empty) {
                    return;
                }

                snapshot.forEach(doc => {
                    const { acesso } = doc.data()
                    setAcesso(acesso)
                    console.log(query)
                });


            })
            .catch(err => {
                console.log('Error getting documents', err);
            });
    }, [db, usuarioEmail])

    useEffect(() => {
        firebase.firestore().collection('setores').get().then(async (resultado) => {
            await resultado.docs.forEach(doc => {
                ListaSetores.push({
                    id: doc.id,
                    ...doc.data()
                })
            })

            setSetores(ListaSetores);
            setCarregando(0)
        });
        firebase.firestore().collection('tipos_ncfs').get().then(async (resultado) => {
            await resultado.docs.forEach(doc => {
                ListaTiposNcf.push({
                    id: doc.id,
                    ...doc.data()
                })
            })

            set_tipo_ncf(ListaTiposNcf);
            setCarregando(0)
        });
        // eslint-disable-next-line
    }, [carregando])

    return (
        <>
            <Navbar />
            {parseInt(acesso) > 1 ?
                <>
                    <div className="body-content text-center centro">
                        <div className=" col-sm-12 mt-4 form col-md-7">
                            <div className="row centro col-sm-12">
                                <h3 className="mx-auto mt-2 font-weight-bold">
                                    {props.match.params.id ? 'Atualizar no conformidade' : 'Publicar no conformidade'}</h3>
                            </div>

                            <form>
                                <div className="form-group">
                                    <label>Titulo</label>
                                    <input onChange={(e) => setTitulo(e.target.value)} type="text" className="form-control" value={titulo && titulo} />
                                </div>
                                <div className="row">
                                    <div className="form-group col-6">
                                        <label>Numero da Of</label>
                                        <input onChange={(e) => setNumeroOf(e.target.value)} type="text" className="form-control" value={numeroOf && numeroOf} />
                                    </div>

                                    <div className="form-group col-6">
                                        <label>Cdigo do <br></br>Desenho</label>
                                        <input onChange={(e) => setCodDesenho(e.target.value)} type="text" className="form-control" value={codDesenho && codDesenho} />
                                    </div>

                                    <div className="form-group col-6">
                                        <label>Quantidade<br></br>da Of</label>
                                        <input onChange={(e) => setQuantOf(e.target.value)} type="text" className="form-control" value={quantOf && quantOf} />
                                    </div>

                                    <div className="form-group col-6">
                                        <label>Quant. no <br></br>conforme</label>
                                        <input onChange={(e) => setQuantNaoConf(e.target.value)} type="text" className="form-control" value={quantNaoConforme && quantNaoConforme} />
                                    </div>
                                </div>
                                <div className="form-group f-component">
                                    <label>Setor responsavel</label>
                                    <select onChange={(e) => setSetorResponsavel(e.target.value)} className="form-control">
                                        <option selected value>-- Selecione o responsevel --</option>
                                        {setores.map((setor) => <option key={setor.id}>{setor.nome}</option>)}
                                    </select>
                                </div>

                                <div className="form-group f-component">
                                    <label>Tipo de no conformidade</label>
                                    <select onChange={(e) => setCausaNCF(e.target.value)} className="form-control">
                                        <option selected value>-- Selecione o tipo de ncf --</option>
                                        {tipo_ncf.map((tipo) => <option key={tipo.id}>{tipo.nome}</option>)}
                                    </select>
                                </div>

                                <div className="form-group">
                                    <label>Descrio da no conformidade</label>
                                    <textarea onChange={(e) => setDetalhes(e.target.value)} className="form-control" rows="3" value={detalhes && detalhes} />
                                </div>

                                <div className="form-group row">
                                    <div className="col-6 ">
                                        <label>Data</label>
                                        <input onChange={(e) => setData(e.target.value)} type="date" className="form-control" value={data && data} />
                                    </div>
                                    <div className="col-6">
                                        <label>Hora</label>
                                        <input onChange={(e) => setHora(e.target.value)} type="time" className="form-control" value={hora && hora} />
                                    </div>
                                </div>

                                {
                                    props.match.params.id ? ''
                                        :
                                        <div className="form-group">
                                            <label>Upload Foto(s)</label>
                                            <input onChange={(e) => setFoto1(e.target.files[0])} type="file" className="form-control" />
                                            {foto1 ? <input onChange={(e) => setFoto2(e.target.files[0])} type="file" className="form-control" /> : null}
                                            {foto1 && foto2 ? <input onChange={(e) => setFoto3(e.target.files[0])} type="file" className="form-control" /> : null}
                                            {foto1 && foto2 && foto3 ? <input onChange={(e) => setFoto4(e.target.files[0])} type="file" className="form-control" /> : null}
                                            {foto1 && foto2 && foto3 && foto4 ? <input onChange={(e) => setFoto5(e.target.files[0])} type="file" className="form-control" /> : null}
                                        </div>
                                }

                                <hr />

                                <div className="form-group">
                                    <label>Soluo da no conformidade</label>
                                    <textarea onChange={(e) => setSolucao(e.target.value)} className="form-control" rows="3" />
                                </div>

                                <div className="row">
                                    {
                                        carregando > 0 ? <div class="spinner-border text-danger mx-auto" role="status"><span class="sr-only">Loading...</span></div>
                                            : ''
                                    }
                                    {
                                        titulo && setorResponsavel && data && hora && solucao ? <button onClick={props.match.params.id ? atualizar : cadastrar}
                                            type="button" className="btn btn-lg btn-block mt-3 mb-5 btn-cadastro">{props.match.params.id ? 'Atualizar No conformidade' : 'Publicar No conformidade'}</button>
                                            : ''
                                    }
                                </div>
                            </form>
                            <div className="msg-login text-black text-center my-5">
                                {msgTipo === 'sucesso' && <><Emoji symbol="" label="smile">
                                </Emoji><strong>WoW!</strong> Cadastrado com sucesso!</>}
                                {msgTipo === 'erro' && <><Emoji symbol="" label="cry">
                                </Emoji><strong>Ops!</strong> Verifique os dados e tente novamente.</>}
                            </div>
                        </div>
                    </div>
                    <Footer />
                </> : <div className="mt-5 text-center"><h1>Voce no tem autorizao para cadastrar</h1></div>}
            <Footer />
        </>
    )
}

export default QualidadeCadastro;