import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import './cliente.css';
import Navbar from '../../components/navbar/';
import Footer from '../../components/footer/';
import Emoji from 'a11y-react-emoji'

import firebase from '../../config/firebase';

import {Redirect} from 'react-router-dom';

function ClienteCadastro(props) {
    const [carregando, setCarregando] = useState();
    const [msgTipo, setMsgTipo] = useState();
    const db = firebase.firestore();
    const usuarioEmail = useSelector(state => state.usuarioEmail);
    const [nome, setNome] = useState('')
    const [cnpjOUcpf, setcnpjOUcpf] = useState('')
    const [telefone, setFone] = useState('')
    const [detalhes, setDetalhes] = useState('')
    const [acesso, setAcesso] = useState('')

    const [redirect, setRedirect] = useState(false)

    function atualizar() {
        setMsgTipo(null);
        setCarregando(1);

        db.collection('clientes').doc(props.match.params.id).update({
            nome: nome,
            cnpjoucpf: cnpjOUcpf,
            telefone: telefone,
            detalhes: detalhes,
            usuarioEmail: usuarioEmail,
        }).then(() => {
            setMsgTipo('sucesso');
            setCarregando(0);
            setRedirect(true);
        }).catch(erro => {
            setMsgTipo('erro');
            setCarregando(0);
        });
    }

    function cadastrar() {
        setMsgTipo(null);
        setCarregando(1);

        db.collection('clientes').add({
            nome: nome,
            cnpjoucpf: cnpjOUcpf,
            telefone: telefone,
            detalhes: detalhes,
            usuarioEmail: usuarioEmail,
            publico: 1,
            criacao: new Date()
        }).then(() => {
            setMsgTipo('sucesso');
            setCarregando(0);
            window.location.reload();
        }).catch(erro => {
            setMsgTipo('erro');
            setCarregando(0);
        });
    }

    useEffect(() => {
        if (props.match.params.id) {
            firebase.firestore().collection('clientes').doc(props.match.params.id).get().then(resultado => {
                setNome(resultado.data().nome)
                setcnpjOUcpf(resultado.data().cnpjoucpf)
                setDetalhes(resultado.data().detalhes)
                setFone(resultado.data().telefone)
            })
        }
    }, [carregando, props.match.params.id])

    useEffect(() => {
        let usersRef = db.collection('users');
        // eslint-disable-next-line 
        let query = usersRef.where('usuarioEmail', '==', usuarioEmail).get()
            .then(snapshot => {
                if (snapshot.empty) {
                    return;
                }

                snapshot.forEach(doc => {
                    const { acesso } = doc.data()
                    setAcesso(acesso)
                });


            })
            .catch(err => {
                console.log('Error getting documents', err);
            });
    }, [db, usuarioEmail])

    return (
        <>
            <Navbar />
            {redirect === true ? <Redirect to='/verclientes' /> : null}
            {parseInt(acesso) > 1 ?
                <div className="body-content text-center centro">
                    <div className=" col-sm-12 mt-4 form col-md-7">
                        <div className="row centro col-sm-12">
                            <h3 className="mx-auto mt-2 font-weight-bold">{props.match.params.id ? 'Atualizar Cliente' : 'Novo Cliente'}</h3>
                        </div>
                        <form>
                            <div className="form-group">
                                <label>Nome</label>
                                <input onChange={(e) => setNome(e.target.value)} type="text" className="form-control" value={nome && nome} />
                            </div>

                            <div className="form-group">
                                <label>CNPJ ou CPF</label>
                                <input onChange={(e) => setcnpjOUcpf(e.target.value)} type="text" maxLength="14" className="form-control" value={cnpjOUcpf && cnpjOUcpf} />
                            </div>

                            <div className="form-group">
                                <label>Telefone</label>
                                <input onChange={(e) => setFone(e.target.value)} type="text" maxLength="11" className="form-control" value={telefone && telefone} />
                            </div>

                            <div className="form-group">
                                <label>Anotaes</label>
                                <textarea onChange={(e) => setDetalhes(e.target.value)} className="form-control" maxLength="950" rows="5" value={detalhes && detalhes} />
                            </div>

                            {
                                carregando > 0 ? <div class="spinner-border text-danger mx-auto" role="status"><span class="sr-only"></span></div>
                                    : ''
                            }
                            {
                                nome && cnpjOUcpf && telefone ? <button onClick={props.match.params.id ? atualizar : cadastrar}
                                    type="button" className="btn btn-lg btn-block mt-3 mb-5 btn-cadastro">
                                    {props.match.params.id ? 'Atualizar Cliente' : 'Publicar Cliente'}</button>
                                    : ''
                            }
                        </form>

                        <div className="msg-login text-black text-center my-5">
                            {msgTipo === 'sucesso' && <><Emoji symbol="" label="smile">
                            </Emoji><strong>WoW!</strong> Cadastrado com sucesso!</>}
                            {msgTipo === 'erro' && <><Emoji symbol="" label="cry">
                            </Emoji><strong>Ops!</strong> Verifique os dados e tente novamente.</>}
                        </div>
                    </div>

                </div> : <div className="mt-5 text-center"><h1>Voce no tem autorizao para cadastrar</h1></div>}
            <Footer />
        </>
    )
}

export default ClienteCadastro;