import React, { useState, useEffect } from 'react';
import './setor.css';
import Navbar from '../navbar';
import Footer from '../footer';
import { useSelector } from 'react-redux';
import firebase from '../../config/firebase';
import EventoSetor from '../evento-card-setor'

function VisualizarEventosSetor({ match }) {

    const [setor, setSetores] = useState([]);
    const [pesquisa, setPesquisa] = useState('');
    const usuarioEmail = useSelector(state => state.usuarioEmail);
    const [carregando, setCarregando] = useState(1)
    let ListaSetores = [];

    useEffect(() => {

        if (match.params.parametro) {
            firebase.firestore().collection('setores').where('usuario', '==', usuarioEmail).get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    if (doc.data().nome.indexOf(pesquisa) >= 0) {
                        ListaSetores.push({
                            id: doc.id,
                            ...doc.data()
                        })
                    }
                })

                setSetores(ListaSetores);
                setCarregando(0)
            });

        } else {
            firebase.firestore().collection('setores').get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    if (doc.data().nome.indexOf(pesquisa) >= 0) {
                        ListaSetores.push({
                            id: doc.id,
                            ...doc.data()
                        })
                    }
                })

                setSetores(ListaSetores);
                setCarregando(0)
            });
        }
    }, [ListaSetores, match.params.parametro, pesquisa, usuarioEmail]);

    return (
        <>
            <Navbar />
            {
                carregando ? <div className="row mt-5"><div class="spinner-border text-danger mx-auto" role="status"></div></div>
                    :
                    <div className="body-content col-sm-12 col-md-8 mx-auto datascreen">
                        <div className="row p-3 mx-auto">
                            <input onChange={(e) => setPesquisa(e.target.value)} type="text"
                                className="form-control mx-auto search col-sm-8 text-center" placeholder="Pesquisar Setor pelo nome..." />
                        </div>
                        <div className="row exibicao mx-2 text-center">
                            {setor.map(item => <EventoSetor key={item.id} id={item.id}
                                nome={item.nome} detalhes={item.detalhes} usuario={item.usuarioEmail} />)}
                        </div>
                    </div>
            }
            <Footer />
        </>
    )
}

export default VisualizarEventosSetor;