import React from 'react';
import {Link} from 'react-router-dom';

import './evento-card-carregamento.css';

function EventoCardCarregamento({id, nota, cliente, data}) {

    let split = data.split('-');
    let novadata = split[2] + "/" +split[1]+"/"+split[0];

    return (
        <div className="card-body card text-center">
            <div class="card-header">
                <h5>Nota Fiscal : {nota}</h5>
            </div>
            <div class="card-body">
                <h5 className="card-text card-title text-center">Cliente : {cliente}</h5>
                <Link to={'/carregamentodetalhes/' + id} className="btn btnfixed2 btn-sm btn-detalhes">+ detalhes</Link>
            </div>
            <div class="card-footer text-muted">
                <p className="card-text"> Data: {novadata}</p>
            </div>
        </div>
    )
}

export default EventoCardCarregamento;