import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import './pcp-cadastro.css';
import Navbar from '../../components/navbar/';
import Footer from '../../components/footer/';
import Emoji from 'a11y-react-emoji'

import firebase from '../../config/firebase';

import {Redirect} from 'react-router-dom';

function PcpCadastro(props) {
    const [carregando, setCarregando] = useState();
    const [msgTipo, setMsgTipo] = useState();
    const [data, setData] = useState();
    const [maquina, setMaquina] = useState('');
    const [horasaproduzir, sethap] = useState('');
    const [horasproduzidas, sethp] = useState('');
    const [acesso, setAcesso] = useState('');
    const usuarioEmail = useSelector(state => state.usuarioEmail);
    // const width = localStorage.getItem('width')

    const db = firebase.firestore();

    const [maquinas, setMaquinas] = useState([])
    let ListaMaquinas = [];

    const [redirect, setRedirect] = useState(false);

    useEffect(() => {
        if (props.match.params.id) {
            if (props.match.params.mq === '01') {
                firebase.firestore().collection('B6520').doc(props.match.params.id).get().then(resultado => {
                    setData(resultado.data().data);
                    setMaquina(resultado.data().maquina);
                    sethap(resultado.data().horasaproduzir);
                    sethp(resultado.data().horasproduzidas);
                })
            }
            else if (props.match.params.mq === '02') {
                firebase.firestore().collection('B4025').doc(props.match.params.id).get().then(resultado => {
                    setData(resultado.data().data);
                    setMaquina(resultado.data().maquina);
                    sethap(resultado.data().horasaproduzir);
                    sethp(resultado.data().horasproduzidas);
                })
            }
            else if (props.match.params.mq === '03') {
                firebase.firestore().collection('Xcite').doc(props.match.params.id).get().then(resultado => {
                    setData(resultado.data().data);
                    setMaquina(resultado.data().maquina);
                    sethap(resultado.data().horasaproduzir);
                    sethp(resultado.data().horasproduzidas);
                })
            }
            else if (props.match.params.mq === '04') {
                firebase.firestore().collection('Xpert').doc(props.match.params.id).get().then(resultado => {
                    setData(resultado.data().data);
                    setMaquina(resultado.data().maquina);
                    sethap(resultado.data().horasaproduzir);
                    sethp(resultado.data().horasproduzidas);
                })
            }
            else if (props.match.params.mq === '05') {
                firebase.firestore().collection('durma').doc(props.match.params.id).get().then(resultado => {
                    setData(resultado.data().data);
                    setMaquina(resultado.data().maquina);
                    sethap(resultado.data().horasaproduzir);
                    sethp(resultado.data().horasproduzidas);
                })
            }
            else {
                alert("ERRO AO BUSCAR NO BANCO")
            }
        } else {
            firebase.firestore().collection('maquinas').get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    ListaMaquinas.push({
                        id: doc.id,
                        ...doc.data()
                    })
                })

                setMaquinas(ListaMaquinas);
            }, [carregando]);
            setCarregando(0)
        }
        // eslint-disable-next-line
    }, [carregando, props.match.params.id])

    function dateToBR(data) {
        return data.split('-').reverse().join('/');
    }

    function atualizar() {
        setMsgTipo(null);
        setCarregando(1);
        setData(dateToBR(data));

        if (maquina === 'Bysprint 6520') {
            firebase.firestore().collection('B6520').doc(props.match.params.id).update({
                maquina,
                horasaproduzir,
                horasproduzidas,
            }).then(() => {
                setMsgTipo('sucesso');
                setCarregando(0);
                setRedirect(true);
            }).catch(erro => {
                setMsgTipo('erro');
                setCarregando(0);
            });
        }
        else if (maquina === 'Bystar 4025') {
            firebase.firestore().collection('B4025').doc(props.match.params.id).update({
                maquina,
                horasaproduzir,
                horasproduzidas,
            }).then(() => {
                setMsgTipo('sucesso');
                setCarregando(0);
                setRedirect(true);
            }).catch(erro => {
                setMsgTipo('erro');
                setCarregando(0);
            });
        }
        else if (maquina === 'Xcite') {
            firebase.firestore().collection('Xcite').doc(props.match.params.id).update({
                maquina,
                horasaproduzir,
                horasproduzidas,
            }).then(() => {
                setMsgTipo('sucesso');
                setCarregando(0);
                setRedirect(true);
            }).catch(erro => {
                setMsgTipo('erro');
                setCarregando(0);
            });
        }
        else if (maquina === 'Xpert') {
            firebase.firestore().collection('Xpert').doc(props.match.params.id).update({
                maquina,
                horasaproduzir,
                horasproduzidas,
            }).then(() => {
                setMsgTipo('sucesso');
                setCarregando(0);
                setRedirect(true);
            }).catch(erro => {
                setMsgTipo('erro');
                setCarregando(0);
            });
        }
        else if (maquina === 'Dobradeira Durma') {
            firebase.firestore().collection('durma').doc(props.match.params.id).update({
                maquina,
                horasaproduzir,
                horasproduzidas,
            }).then(() => {
                setMsgTipo('sucesso');
                setCarregando(0);
                setRedirect(true);
            }).catch(erro => {
                setMsgTipo('erro');
                setCarregando(0);
            });
        }
        else {
            alert("ERRO AO ATUALIZAR")
        }
    }

    function cadastrar() {
        setMsgTipo(null);
        setCarregando(1);
        setData(dateToBR(data));

        if (maquina === 'Bysprint 6520') {
            firebase.firestore().collection('B6520').add({
                data: dateToBR(data),
                dataMili: Date.parse(data),
                maquina,
                horasaproduzir,
                horasproduzidas,
                usuario: usuarioEmail,
                publico: 1,
                criacao: new Date()
            }).then(() => {
                setMsgTipo('sucesso');
                setCarregando(0);
                window.location.reload();
            }).catch(erro => {
                setMsgTipo('erro');
                setCarregando(0);
            });
        }
        else if (maquina === 'Bystar 4025') {
            firebase.firestore().collection('B4025').add({
                data: dateToBR(data),
                dataMili: Date.parse(data),
                maquina,
                horasaproduzir,
                horasproduzidas,
                usuario: usuarioEmail,
                publico: 1,
                criacao: new Date()
            }).then(() => {
                setMsgTipo('sucesso');
                setCarregando(0);
                window.location.reload();
            }).catch(erro => {
                setMsgTipo('erro');
                setCarregando(0);
            });
        }
        else if (maquina === 'Xcite') {
            firebase.firestore().collection('Xcite').add({
                data: dateToBR(data),
                dataMili: Date.parse(data),
                maquina,
                horasaproduzir,
                horasproduzidas,
                usuario: usuarioEmail,
                publico: 1,
                criacao: new Date()
            }).then(() => {
                setMsgTipo('sucesso');
                setCarregando(0);
                window.location.reload();
            }).catch(erro => {
                setMsgTipo('erro');
                setCarregando(0);
            });
        }
        else if (maquina === 'Xpert') {
            firebase.firestore().collection('Xpert').add({
                data: dateToBR(data),
                dataMili: Date.parse(data),
                maquina,
                horasaproduzir,
                horasproduzidas,
                usuario: usuarioEmail,
                publico: 1,
                criacao: new Date()
            }).then(() => {
                setMsgTipo('sucesso');
                setCarregando(0);
                window.location.reload();
            }).catch(erro => {
                setMsgTipo('erro');
                setCarregando(0);
            });
        }
        else if (maquina === 'Dobradeira Durma') {
            firebase.firestore().collection('durma').add({
                data: dateToBR(data),
                dataMili: Date.parse(data),
                maquina,
                horasaproduzir,
                horasproduzidas,
                usuario: usuarioEmail,
                publico: 1,
                criacao: new Date()
            }).then(() => {
                setMsgTipo('sucesso');
                setCarregando(0);
                window.location.reload();
            }).catch(erro => {
                setMsgTipo('erro');
                setCarregando(0);
            });
        }
        else {
            alert("ERRO AO CADASTRAR")
            window.location.reload();
        }

    }

    useEffect(() => {
        let usersRef = db.collection('users');
        // eslint-disable-next-line 
        let query = usersRef.where('usuarioEmail', '==', usuarioEmail).get()
            .then(snapshot => {
                if (snapshot.empty) {
                    return;
                }

                snapshot.forEach(doc => {
                    const { acesso } = doc.data()
                    setAcesso(acesso)
                });


            })
            .catch(err => {
                console.log('Error getting documents', err);
            });
    }, [db, usuarioEmail])

    return (
        <>
            <Navbar />
            {redirect === true && maquina === 'Bysprint 6520' ? <Redirect to='/verbysprint6520' /> : null}
            {redirect === true && maquina === 'Bystar 4025'   ? <Redirect to='/verbysprint4025' /> : null}
            {redirect === true && maquina === 'Xcite'         ? <Redirect to='/verxcite' /> : null}
            {redirect === true && maquina === 'Xpert'         ? <Redirect to='/verxpert' /> : null}
            {redirect === true && maquina === 'Dobradeira Durma' ? <Redirect to='/verdurma' /> : null}

            {parseInt(acesso) === 3 ?
                <>
                    <div className="body-content text-center centro">
                        <div className="col-sm-12 mt-4 form col-md-10">
                            <div className="row centro col-sm-12">
                                <div className="col-sm-12 ml-4 col-md-6">
                                    <h1 className="mx-auto head-form mt-2 font-weight-bold">
                                        {props.match.params.id ? 'Atualizar Programao' : 'Publicar Programao'}

                                    </h1></div>
                            </div>

                            <form>
                                <div className="row">
                                    {
                                        props.match.params.id ? null
                                            :
                                            <div className="col-sm-12 col-md-2">
                                                <label>Data</label>
                                                <input onChange={(e) => setData(e.target.value)}
                                                    type="date" className="form-control" value={data && data} />
                                            </div>}
                                    <div className="form-group col-sm-12 col-md-2 f-component">
                                        <label>Maquina</label>
                                        <select onChange={(e) => setMaquina(e.target.value)}
                                            className="form-control">
                                            <option selected value>{maquina && maquina}</option>
                                            {maquinas.map((novo) => <option key={novo.id}>{novo.nome}</option>)}
                                        </select>
                                    </div>
                                    <div className="form-group col-sm-12 col-md-3 f-component">
                                        <label>Horas a produzir</label>
                                        <input onChange={(e) => sethap(e.target.value)}
                                            type="text" className="form-control" value={horasaproduzir && horasaproduzir} />
                                    </div>

                                    <div className="form-group col-sm-12 col-md-3 f-component">
                                        <label>Horas produzidas</label>
                                        <input onChange={(e) => sethp(e.target.value)} type="text"
                                            className="form-control" value={horasproduzidas && horasproduzidas} />
                                    </div>
                                    <div className="form-group col-sm-12 col-md-2 f-component">
                                        <label>% Aprov</label>
                                        <input type="text" disabled className="form-control"
                                            value={horasaproduzir ?
                                                ((horasproduzidas / horasaproduzir) * 100).toFixed(2) : 0} />
                                    </div>
                                </div>
                                {
                                    carregando > 0 ? <div class="spinner-border text-danger mx-auto" role="status"><span class="sr-only">Loading...</span></div>
                                        : ''
                                }
                                {
                                    data && maquina && horasaproduzir ?
                                        <button onClick={props.match.params.id ? atualizar : cadastrar} type="button"
                                            className="btn btn-lg btn-block mt-3 mb-5 btn-cadastro">
                                            {props.match.params.id ? 'Atualizar Programao' : 'Publicar Programao'}</button>
                                        : ''
                                }

                            </form>

                            <div className="msg-login text-black text-center my-5">
                                {msgTipo === 'sucesso' && <><Emoji symbol="" label="smile">
                                </Emoji><strong>WoW!</strong> Cadastrado com sucesso!</>}
                                {msgTipo === 'erro' && <><Emoji symbol="" label="cry">
                                </Emoji><strong>Ops!</strong> Verifique os dados e tente novamente.</>}
                            </div>
                        </div>
                    </div>
                    <Footer />
                </> : <div className="mt-5 text-center">
                    <h1>Voce no tem autorizao para cadastrar</h1>
                    <Footer />
                </div>
            }
        </>
    )
}

export default PcpCadastro;