import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import './recebimento-cadastro.css';
import Navbar from '../../components/navbar/';
import Footer from '../../components/footer/';

import Emoji from 'a11y-react-emoji'

import firebase from '../../config/firebase';

import {Redirect} from 'react-router-dom';

function RecebimentoCadastro(props) {
    const [carregando, setCarregando] = useState();
    const [msgTipo, setMsgTipo] = useState();
    const [nota, setNota] = useState();
    const [fornecedor, setFornecedor] = useState();
    const [detalhes, setDetalhes] = useState('');
    const [data, setData] = useState();
    const [hora, setHora] = useState();
    const [foto1, setFoto1] = useState('');
    const [foto2, setFoto2] = useState('');
    const [foto3, setFoto3] = useState('');
    const [foto4, setFoto4] = useState('');
    const [foto5, setFoto5] = useState('');
    const [responsavel, setResponsavel] = useState();
    const [acesso, setAcesso] = useState('');
    const usuarioEmail = useSelector(state => state.usuarioEmail);
    // const width = localStorage.getItem('width')

    const storage = firebase.storage();
    const db = firebase.firestore();

    const [funcionarios, setFuncionarios] = useState([])
    let ListaFuncionarios = []

    const [BancoFornecedores, setBancoFornecedores] = useState([])
    let ListaFornecedores = []

    const [redirect, setRedirect] = useState(false);

    useEffect(() => {
        if (props.match.params.id) {
            firebase.firestore().collection('recebimentos').doc(props.match.params.id).get().then(resultado => {
                setNota(resultado.data().nota)
                setFornecedor(resultado.data().fornecedor)
                setDetalhes(resultado.data().detalhes)
                setHora(resultado.data().hora)
                setData(resultado.data().data)
                setResponsavel(resultado.data().responsavel)
            })
            firebase.firestore().collection('funcionarios').get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    ListaFuncionarios.push({
                        id: doc.id,
                        ...doc.data()
                    })
                })

                setFuncionarios(ListaFuncionarios);
            });
            firebase.firestore().collection('fornecedores').get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    ListaFornecedores.push({
                        id: doc.id,
                        ...doc.data()
                    })
                })

                setBancoFornecedores(ListaFornecedores);
            });
            setCarregando(0)
        } else {
            firebase.firestore().collection('funcionarios').get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    ListaFuncionarios.push({
                        id: doc.id,
                        ...doc.data()
                    })
                })

                setFuncionarios(ListaFuncionarios);
            });
            firebase.firestore().collection('fornecedores').get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    ListaFornecedores.push({
                        id: doc.id,
                        ...doc.data()
                    })
                })

                setBancoFornecedores(ListaFornecedores);
            });
            setCarregando(0)
        }
        // eslint-disable-next-line
    }, [props.match.params.id])

    function atualizar() {
        setMsgTipo(null);
        setCarregando(1);

        db.collection('recebimentos').doc(props.match.params.id).update({
            nota: nota,
            fornecedor: fornecedor,
            detalhes: detalhes,
            responsavel: responsavel,
            data: data,
            hora: hora
        }).then(() => {
            setMsgTipo('sucesso');
            setCarregando(0);
            setRedirect(true);
        }).catch(erro => {
            setMsgTipo('erro');
            setCarregando(0);
        });
    }

    function cadastrar() {
        setMsgTipo(null);
        setCarregando(1);

        if (foto1) { storage.ref(`recebimentos/${foto1.name}`).put(foto1) }
        if (foto2) { storage.ref(`recebimentos/${foto2.name}`).put(foto2) }
        if (foto3) { storage.ref(`recebimentos/${foto3.name}`).put(foto3) }
        if (foto4) { storage.ref(`recebimentos/${foto4.name}`).put(foto4) }
        if (foto5) { storage.ref(`recebimentos/${foto5.name}`).put(foto5) }


        setTimeout(function(){
        db.collection('recebimentos').add({
            nota: nota,
            fornecedor: fornecedor,
            detalhes: detalhes,
            responsavel: responsavel,
            data: data,
            hora: hora,
            usuario: usuarioEmail,
            visualizacoes: 0,
            foto1: foto1 ? foto1.name : '',
            foto2: foto2 ? foto2.name : '',
            foto3: foto3 ? foto3.name : '',
            foto4: foto4 ? foto4.name : '',
            foto5: foto5 ? foto5.name : '',
            publico: 1,
            criacao: new Date()
        }).then(() => {
            setMsgTipo('sucesso');
            setCarregando(0);
            window.location.reload();
        }).catch(erro => {
            setMsgTipo('erro');
            setCarregando(0);
        });
    }, 15000);
    }

    useEffect(() => {
        let usersRef = db.collection('users');
        // eslint-disable-next-line 
        let query = usersRef.where('usuarioEmail', '==', usuarioEmail).get()
            .then(snapshot => {
                if (snapshot.empty) {
                    return;
                }

                snapshot.forEach(doc => {
                    const { acesso } = doc.data()
                    setAcesso(acesso)
                });


            })
            .catch(err => {
                console.log('Error getting documents', err);
            });
    }, [db, usuarioEmail])

    return (
        <>
            <Navbar />
            {redirect === true ? <Redirect to='/verrecebimentos' /> : null}
            {parseInt(acesso) > 1 ?
                <>
                    <div className="body-content text-center centro">
                        <div className=" col-sm-12 mt-4 form col-md-7">
                            <div className="row centro col-sm-12">
                                <div className="col-sm-12 ml-4 col-md-6">
                                    <h1 className="mx-auto head-form mt-2 font-weight-bold">
                                        {props.match.params.id ? 'Atualizar Recebimento' : 'Publicar Recebimento'}
                                    </h1></div>
                            </div>

                            <form>
                                <div className="form-group f-component">
                                    <label>Numero da Nota</label>
                                    <input onChange={(e) => setNota(e.target.value)} type="text" className="form-control" value={nota && nota} />
                                </div>

                                {props.match.params.id !== null ?
                                    <div className="form-group f-component">
                                        <label>Fornecedor</label>
                                        <select onChange={(e) => setFornecedor(e.target.value)} className="form-control">
                                            <option selected value>{fornecedor && fornecedor}</option>
                                            {BancoFornecedores.map((novo) => <option key={novo.id}>{novo.nome}</option>)}
                                        </select>
                                    </div>

                                    :
                                    <div className="form-group f-component">
                                        <label>Fornecedor</label>
                                        <select onChange={(e) => setFornecedor(e.target.value)} className="form-control">
                                            <option selected value>-- Selecione o fornecedor --</option>
                                            {BancoFornecedores.map((novo) => <option key={novo.id}>{novo.nome}</option>)}
                                        </select>
                                    </div>

                                }

                                {props.match.params.id !== null ?
                                    <div className="form-group f-component">
                                        <label>Responsavel</label>
                                        <select onChange={(e) => setResponsavel(e.target.value)} className="form-control">
                                            <option selected value>{responsavel && responsavel}</option>
                                            {funcionarios.map((novo) => <option key={novo.id}>{novo.nome}</option>)}
                                        </select>
                                    </div>

                                    :
                                    <div className="form-group f-component">
                                        <label>Responsavel</label>
                                        <select onChange={(e) => setResponsavel(e.target.value)} className="form-control">
                                            <option selected value>-- Selecione o responsavel --</option>
                                            {funcionarios.map((novo) => <option key={novo.id}>{novo.nome}</option>)}
                                        </select>
                                    </div>

                                }

                                <div className="form-group f-component">
                                    <label>Anotaes</label>
                                    <textarea onChange={(e) => setDetalhes(e.target.value)}
                                        className="form-control" rows="3" value={detalhes && detalhes} />
                                </div>

                                <div className="form-group f-component row">
                                    <div className="col-6 ">
                                        <label>Data</label>
                                        <input onChange={(e) => setData(e.target.value)} type="date" className="form-control" value={data && data} />
                                    </div>
                                    <div className="col-6">
                                        <label>Hora</label>
                                        <input onChange={(e) => setHora(e.target.value)} type="time" className="form-control" value={hora && hora} />
                                    </div>
                                </div>

                                {
                                    props.match.params.id ? ''
                                        :
                                        <div className="form-group f-component">
                                            <label>Upload Foto(s)</label>
                                            <input onChange={(e) => setFoto1(e.target.files[0])} type="file" className="form-control" />
                                            {foto1 ? <input onChange={(e) => setFoto2(e.target.files[0])} type="file" className="form-control" /> : null}
                                            {foto1 && foto2 ? <input onChange={(e) => setFoto3(e.target.files[0])} type="file" className="form-control" /> : null}
                                            {foto1 && foto2 && foto3 ? <input onChange={(e) => setFoto4(e.target.files[0])} type="file" className="form-control" /> : null}
                                            {foto1 && foto2 && foto3 && foto4 ? <input onChange={(e) => setFoto5(e.target.files[0])} type="file" className="form-control" /> : null}
                                        </div>
                                }

                                {
                                    carregando > 0 ? <div class="spinner-border text-danger mx-auto" role="status"><span class="sr-only">Loading...</span></div>
                                        : ''
                                }
                                {
                                    nota && data && hora && fornecedor ? <button onClick={props.match.params.id ? atualizar : cadastrar} type="button" className="btn btn-lg btn-block mt-3 mb-5 btn-cadastro">{props.match.params.id ? 'Atualizar Recebimento' : 'Publicar Recebimento'}</button>
                                        : ''
                                }

                            </form>

                            <div className="msg-login text-black text-center my-5">
                                {msgTipo === 'sucesso' && <><Emoji symbol="" label="smile">
                                </Emoji><strong>WoW!</strong> Cadastrado com sucesso!</>}
                                {msgTipo === 'erro' && <><Emoji symbol="" label="cry">
                                </Emoji><strong>Ops!</strong> Verifique os dados e tente novamente.</>}
                            </div>
                        </div>
                    </div>
                    <Footer />
                </> : <div className="mt-5 text-center">
                    <h1>Voce no tem autorizao para cadastrar</h1>
                    <Footer />
                </div>
            }
        </>
    )
}

export default RecebimentoCadastro;