import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import './carregamento-cadastro.css';
import Navbar from '../../components/navbar/';
import Footer from '../../components/footer/';

import Emoji from 'a11y-react-emoji'

import {Redirect} from 'react-router-dom';

import firebase from '../../config/firebase';

function CarregamentoCadastro(props) {

    const [carregando, setCarregando] = useState();
    const [msgTipo, setMsgTipo] = useState();
    const [nota, setNota] = useState();
    const [cliente, setCliente] = useState();
    const [detalhes, setDetalhes] = useState('');
    const [data, setData] = useState();
    const [hora, setHora] = useState();
    const [foto1, setFoto1] = useState('');
    const [foto2, setFoto2] = useState('');
    const [foto3, setFoto3] = useState('');
    const [foto4, setFoto4] = useState('');
    const [foto5, setFoto5] = useState('');
    const [foto6, setFoto6] = useState('');
    const [foto7, setFoto7] = useState('');
    const [foto8, setFoto8] = useState('');
    const [foto9, setFoto9] = useState('');
    const [foto10, setFoto10] = useState('');
    const [foto11, setFoto11] = useState('');
    const [foto12, setFoto12] = useState('');
    const [foto13, setFoto13] = useState('');
    const [foto14, setFoto14] = useState('');
    const [foto15, setFoto15] = useState('');
    const [responsavel, setResponsavel] = useState();
    const usuarioEmail = useSelector(state => state.usuarioEmail);
    const [acesso, setAcesso] = useState([])

    const storage = firebase.storage();
    const db = firebase.firestore();

    const [BancoClientes, setBancoClientes] = useState([])
    let ListaClientes = []

    const [funcionarios, setFuncionarios] = useState([])
    let ListaFuncionarios = []

    const [redirect, setRedirect] = useState(false)

    useEffect(() => {
        if (props.match.params.id) {
            db.collection('carregamentos').doc(props.match.params.id).get().then(resultado => {
                setNota(resultado.data().nota)
                setCliente(resultado.data().cliente)
                setDetalhes(resultado.data().detalhes)
                setHora(resultado.data().hora)
                setData(resultado.data().data)
                setResponsavel(resultado.data().responsavel)
            })
            firebase.firestore().collection('clientes').get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    ListaClientes.push({
                        id: doc.id,
                        ...doc.data()
                    })
                })

                setBancoClientes(ListaClientes);
            });
            firebase.firestore().collection('funcionarios').get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    ListaFuncionarios.push({
                        id: doc.id,
                        ...doc.data()
                    })
                })

                setFuncionarios(ListaFuncionarios);
            });
        } else {
            firebase.firestore().collection('clientes').get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    ListaClientes.push({
                        id: doc.id,
                        ...doc.data()
                    })
                })

                setBancoClientes(ListaClientes);
            });
            firebase.firestore().collection('funcionarios').get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    ListaFuncionarios.push({
                        id: doc.id,
                        ...doc.data()
                    })
                })

                setFuncionarios(ListaFuncionarios);
            });
            setCarregando(0)
        }
        // eslint-disable-next-line
    }, [props.match.params.id])

    function atualizar() {
        setMsgTipo(null);
        setCarregando(1);

        db.collection('carregamentos').doc(props.match.params.id).update({
            nota,
            cliente,
            detalhes,
            responsavel,
            data,
            hora,
        }).then(() => {
            setMsgTipo('sucesso');
            setCarregando(0);
            setRedirect(true);
        }).catch(erro => {
            setMsgTipo('erro');
            setCarregando(0);
        });
    }

    function cadastrar() {
        setMsgTipo(null);
        setCarregando(1);

        if (foto1) { storage.ref(`carregamentos/${foto1.name}`).put(foto1) }
        if (foto2) { storage.ref(`carregamentos/${foto2.name}`).put(foto2) }
        if (foto3) { storage.ref(`carregamentos/${foto3.name}`).put(foto3) }
        if (foto4) { storage.ref(`carregamentos/${foto4.name}`).put(foto4) }
        if (foto5) { storage.ref(`carregamentos/${foto5.name}`).put(foto5) }
        if (foto6) { storage.ref(`carregamentos/${foto6.name}`).put(foto6) }
        if (foto7) { storage.ref(`carregamentos/${foto7.name}`).put(foto7) }
        if (foto8) { storage.ref(`carregamentos/${foto8.name}`).put(foto8) }
        if (foto9) { storage.ref(`carregamentos/${foto9.name}`).put(foto9) }
        if (foto10) { storage.ref(`carregamentos/${foto10.name}`).put(foto10) }
        if (foto11) { storage.ref(`carregamentos/${foto11.name}`).put(foto11) }
        if (foto12) { storage.ref(`carregamentos/${foto12.name}`).put(foto12) }
        if (foto13) { storage.ref(`carregamentos/${foto13.name}`).put(foto13) }
        if (foto14) { storage.ref(`carregamentos/${foto14.name}`).put(foto14) }
        if (foto15) { storage.ref(`carregamentos/${foto15.name}`).put(foto15) }

        setTimeout(function(){
        db.collection('carregamentos').add({
            nota: nota,
            cliente: cliente,
            detalhes: detalhes,
            responsavel: responsavel,
            data: data,
            hora: hora,
            usuario: usuarioEmail,
            visualizacoes: 0,
            foto1: foto1 ? foto1.name : '',
            foto2: foto2 ? foto2.name : '',
            foto3: foto3 ? foto3.name : '',
            foto4: foto4 ? foto4.name : '',
            foto5: foto5 ? foto5.name : '',
            foto6: foto6 ? foto6.name : '',
            foto7: foto7 ? foto7.name : '',
            foto8: foto8 ? foto8.name : '',
            foto9: foto9 ? foto9.name : '',
            foto10: foto10 ? foto10.name : '',
            foto11: foto11 ? foto11.name : '',
            foto12: foto12 ? foto12.name : '',
            foto13: foto13 ? foto13.name : '',
            foto14: foto14 ? foto14.name : '',
            foto15: foto15 ? foto15.name : '',
            publico: 1,
            criacao: new Date()
        }).then(() => {
            setMsgTipo('sucesso');
            setCarregando(0);
            window.location.reload();
        }).catch(erro => {
            setMsgTipo('erro');
            setCarregando(0);
        });
    }, 35000);
    }

    useEffect(() => {
        let usersRef = db.collection('users');
        // eslint-disable-next-line 
        let query = usersRef.where('usuarioEmail', '==', usuarioEmail).get()
            .then(snapshot => {
                if (snapshot.empty) {
                    return;
                }

                snapshot.forEach(doc => {
                    const { acesso } = doc.data()
                    setAcesso(acesso)
                });


            })
            .catch(err => {
                console.log('Error getting documents', err);
            });
    }, [db, usuarioEmail])

    return (
        <>
            <Navbar />
            {redirect === true ? <Redirect to='/vercarregamentos' /> : null}
            {parseInt(acesso) > 1 ?
                <div className="body-content text-center centro">
                    <div className=" col-sm-12 mt-4 form col-md-7">
                        <div className="row centro col-sm-12">
                            <h3 className="mx-auto mt-2 font-weight-bold">{props.match.params.id ? 'Atualizar Carregamento' : 'Publicar Carregamento'}</h3>
                        </div>

                        <form>
                            <div className="form-group">
                                <label>Numero da Nota</label>
                                <input onChange={(e) => setNota(e.target.value)}
                                    type="text" className="form-control" value={nota && nota} />
                            </div>

                            {props.match.params.id !== null ?
                                <div className="form-group">
                                    <label>Cliente</label>
                                    <select onChange={(e) => setCliente(e.target.value)} className="form-control">
                                        <option selected value>{cliente && cliente}</option>
                                        {BancoClientes.map((novo) => <option key={novo.id}>{novo.nome}</option>)}
                                    </select>
                                </div>

                                :
                                <div className="form-group">
                                    <label>Cliente</label>
                                    <select onChange={(e) => setCliente(e.target.value)} className="form-control">
                                        <option disabled selected value>-- Selecione o item --</option>
                                        {BancoClientes.map((novo) => <option key={novo.id}>{novo.nome}</option>)}
                                    </select>
                                </div>

                            }

                            {props.match.params.id !== null ?
                                <div className="form-group">
                                    <label>Responsavel</label>
                                    <select onChange={(e) => setResponsavel(e.target.value)} className="form-control">
                                        <option selected value>{responsavel && responsavel}</option>
                                        {funcionarios.map((novo) => <option key={novo.id}>{novo.nome}</option>)}
                                    </select>
                                </div>

                                :
                                <div className="form-group">
                                    <label>Responsavel</label>
                                    <select onChange={(e) => setResponsavel(e.target.value)} className="form-control">
                                        <option disabled selected value>-- Selecione o responsavel --</option>
                                        {funcionarios.map((novo) => <option key={novo.id}>{novo.nome}</option>)}
                                    </select>
                                </div>

                            }

                            <div className="form-group f-component">
                                <label>Anotaes</label>
                                <textarea onChange={(e) => setDetalhes(e.target.value)}
                                    className="form-control" rows="3" value={detalhes && detalhes} />
                            </div>

                            <div className="form-group row">
                                <div className="col-6 ">
                                    <label>Data</label>
                                    <input onChange={(e) => setData(e.target.value)} type="date" className="form-control" value={data && data} />
                                </div>
                                <div className="col-6">
                                    <label>Hora</label>
                                    <input onChange={(e) => setHora(e.target.value)} type="time" className="form-control" value={hora && hora} />
                                </div>
                            </div>

                            {
                                props.match.params.id ? ''
                                    :
                                    <div className="form-group">
                                        <label>Upload Foto(s)</label>
                                        <input onChange={(e) => setFoto1(e.target.files[0])} type="file" className="form-control" />
                                        {foto1 ? <input onChange={(e) => setFoto2(e.target.files[0])} type="file" className="form-control" /> : null}
                                        {foto1 && foto2 ? <input onChange={(e) => setFoto3(e.target.files[0])} type="file" className="form-control" /> : null}
                                        {foto1 && foto2 && foto3 ? <input onChange={(e) => setFoto4(e.target.files[0])} type="file" className="form-control" /> : null}
                                        {foto1 && foto2 && foto3 && foto4 ? <input onChange={(e) => setFoto5(e.target.files[0])} type="file" className="form-control" /> : null}
                                        {foto1 && foto2 && foto3 && foto4 && foto5 ? <input onChange={(e) => setFoto6(e.target.files[0])} type="file" className="form-control" /> : null}
                                        {foto1 && foto2 && foto3 && foto4 && foto5 && foto6 ? <input onChange={(e) => setFoto7(e.target.files[0])} type="file" className="form-control" /> : null}
                                        {foto1 && foto2 && foto3 && foto4 && foto5 && foto6 && foto7
                                         ? <input onChange={(e) => setFoto8(e.target.files[0])} type="file" className="form-control" /> : null}
                                        {foto1 && foto2 && foto3 && foto4 && foto5 && foto6 && foto7 && foto8
                                         ? <input onChange={(e) => setFoto9(e.target.files[0])} type="file" className="form-control" /> : null}
                                        {foto1 && foto2 && foto3 && foto4 && foto5 && foto6 && foto7 && foto8 && foto9
                                         ? <input onChange={(e) => setFoto10(e.target.files[0])} type="file" className="form-control" /> : null}
                                        {foto1 && foto2 && foto3 && foto4 && foto5 && foto6 && foto7 && foto8 && foto9 && foto10
                                         ? <input onChange={(e) => setFoto11(e.target.files[0])} type="file" className="form-control" /> : null}
                                        {foto1 && foto2 && foto3 && foto4 && foto5 && foto6 && foto7 && foto8 && foto9 && foto10 && foto11
                                         ? <input onChange={(e) => setFoto12(e.target.files[0])} type="file" className="form-control" /> : null}
                                        {foto1 && foto2 && foto3 && foto4 && foto5 && foto6 && foto7 && foto8 && foto9 && foto10 && foto11 && foto12
                                         ? <input onChange={(e) => setFoto13(e.target.files[0])} type="file" className="form-control" /> : null}
                                        {foto1 && foto2 && foto3 && foto4 && foto5 && foto6 && foto7 && foto8 && foto9 && foto10 && foto11 && foto12 && foto13
                                         ? <input onChange={(e) => setFoto14(e.target.files[0])} type="file" className="form-control" /> : null}
                                        {foto1 && foto2 && foto3 && foto4 && foto5 && foto6 && foto7 && foto8 && foto9 && foto10 && foto11 && foto12 && foto13
                                        && foto14
                                         ? <input onChange={(e) => setFoto15(e.target.files[0])} type="file" className="form-control" /> : null}
                                    </div>
                            }

                            {
                                carregando > 0 ? <div class="spinner-border text-danger mx-auto" role="status"><span class="sr-only">Loading...</span></div>
                                    : ''
                            }
                            {
                                nota && data && hora && cliente ?
                                    <button onClick={props.match.params.id ? atualizar : cadastrar} type="button" className="btn btn-lg btn-block mt-3 mb-5 btn-cadastro">
                                        {props.match.params.id ? 'Atualizar Carregamento' : 'Publicar Carregamento'}</button>
                                    : ''
                            }
                        </form>

                        <div className="msg-login text-black text-center my-5">
                            {msgTipo === 'sucesso' && <><Emoji symbol="" label="smile">
                            </Emoji><strong>WoW!</strong> Cadastrado com sucesso!</>}
                            {msgTipo === 'erro' && <><Emoji symbol="" label="cry">
                            </Emoji><strong>Ops!</strong> Verifique os dados e tente novamente.</>}
                        </div>
                    </div>
                </div> : <div className="mt-5 text-center"><h1>Voce no tem autorizao para cadastrar</h1></div>}
            <Footer />
        </>
    )
}
export default CarregamentoCadastro;