import React, { useState, useEffect } from 'react'
import firebase from '../../config/firebase'
import 'firebase/auth'
import './usuario-novo.css'
import { useSelector } from 'react-redux'
import Navbar from '../../components/navbar';
import Footer from '../../components/footer';
import { Redirect } from 'react-router-dom'
import Emoji from 'a11y-react-emoji';

const db = firebase.firestore();

function NovoUsuario() {

    const [email, setEmail] = useState();
    const [redirect, setRedirect] = useState(false);
    const [senha, setSenha] = useState();
    const [ConfirmSenha, setConfirmSenha] = useState();
    const [msgTipo, setMsgTipo] = useState();
    const [msg, setMsg] = useState();
    const [carregando, setCarregando] = useState();
    const [acesso, setAcesso] = useState(1);
    const [key, setKey] = useState([])
    const [validation, setValidation] = useState([])

    function cadastrarUser() {
        setMsgTipo(null);
        setCarregando(1);

        db.collection('users').add({
            acesso: acesso,
            usuarioEmail: email,
            publico: 1,
            criacao: new Date()
        }).then(() => {
            setMsgTipo('sucesso');
            setCarregando(0);
        }).catch(erro => {
            setMsgTipo('erro');
            setCarregando(0);
        });
    }

    function cadastrar() {
        setCarregando(1);
        setMsgTipo(null)

        if (!email || !senha) {
            setMsgTipo('erro')
            setMsg('Voce precisa adicionar email e senha para o cadastro. ')
            setCarregando(0)
            return;
        }

        if (senha === ConfirmSenha) {
            firebase.auth().createUserWithEmailAndPassword(email, senha).then(resultado => {
                cadastrarUser()
                setCarregando(0)
                setRedirect(true)
                setMsgTipo('sucesso')

            }).catch(erro => {
                setCarregando(0);
                setMsgTipo('erro')
                switch (erro.message) {
                    case 'Password should be at least 6 characters':
                        setMsg('A senha deve ter pelo menos 6 caracteres!');
                        break;
                    case 'The email address is already in use by another account.':
                        setMsg('Este email j est sendo utilizado por outro usurio!');
                        break;
                    case 'The email address is badly formatted.':
                        setMsg('O formato do seu email  invlido!');
                        break;
                    default:
                        setMsg('No foi possvel cadastrar. Tente novamente mais tarde!');
                        break;
                }
            })
        } else {
            setMsgTipo('erro')
            setMsg('Os campos de senha precisam estar iguais. ')
            setCarregando(0)
            return;
        }
    }
    useEffect(() => {
        firebase.firestore().collection('keys').doc('CMJ4IJuAJOtPv6rzdBGH').get().then(resultado => {
            setKey(resultado.data().adminKey)
            setCarregando(0)
        })
    }, [key])
    return (
        <>
            {useSelector(state => state.usuarioLogado) === 1 ? <Navbar /> : null}
            <div className="body-content text-center centro">
                {redirect === true ? <Redirect to='/login' /> : null}
                <div className=" col-sm-12 mt-4 centro form col-md-4">
                    <form className="row centro col-sm-12">
                        <h1 className="mb-1 p-2">Cadastrar novo usuario</h1>
                        <input type="email" onChange={(e) => setEmail(e.target.value)}
                            className="form-control my-2" placeholder="Email" />
                        <input type="password" onChange={(e) => setSenha(e.target.value)}
                            className="form-control my-2" placeholder="Senha" />
                        <input type="password" onChange={(e) => setConfirmSenha(e.target.value)}
                            className="form-control my-2" placeholder="Confirme sua Senha" />
                        <h6> administrador? Insira a chave de admin abaixo:</h6>
                        <input type="password" onChange={(e) => setValidation(e.target.value)}
                            className="form-control my-2" placeholder="Insira sua Admin Key" />

                        {
                            validation && validation === key ?
                                <div className="form-group">
                                    <label>Tipo de usuario</label>
                                    <select onChange={(e) => setAcesso(e.target.value)} className="form-control">
                                        <option disabled selected value>-- Selecione o nivel de acesso do usuario --</option>
                                        <option value="3">ADMINISTRADOR</option>
                                        <option value="2">USUARIO COMUM</option>
                                        <option value="1">APENAS VISUALIZAO</option>
                                    </select>
                                </div>
                                : null
                        }

                        {
                            carregando ? <div class="spinner-border text-danger"
                                role="status"><span class="sr-only">Loading...</span></div>
                                : <button onClick={cadastrar} type="button"
                                    className="btn btn-lg btn-block mt-3 mb-5 btn-cadastro">Cadastrar</button>
                        }

                        <div className="msg-login text-black text-center my-5">
                            {msgTipo === 'sucesso' && <><Emoji symbol="" label="smile">
                            </Emoji><strong>WoW!</strong> Usuario cadastrado com sucesso!</>}
                            {msgTipo === 'erro' && <><Emoji symbol="" label="cry">
                            </Emoji><strong>Ops!</strong> {msg} </>}
                        </div>
                    </form>
                </div>
            </div>
            <Footer />
        </>
    )
}

export default NovoUsuario;