import React, { useState, useEffect } from 'react';
import './visualizar-eventos-orcamentos.css';
import Navbar from '../../components/navbar/';
import Footer from '../../components/footer/';
import { useSelector } from 'react-redux';
import firebase from '../../config/firebase';
import EventoOrcamento from '../../components/evento-card-orcamento/'

function VisualizarEventosOrcamentos({ match }){

    const [orcamentos, setOrcamentos] = useState([]);
    const [pesquisa, setPesquisa] = useState('');
    const usuarioEmail = useSelector(state => state.usuarioEmail);
    const [carregando , setCarregando] = useState(1)
    let listaOrcamentos = []; 

    useEffect(() => {

        if (match.params.parametro) {
            firebase.firestore().collection('orcamentos').where('usuario', '==', usuarioEmail).get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    if (doc.data().codigo.indexOf(pesquisa) >= 0) {
                        listaOrcamentos.push({
                            id: doc.id,
                            ...doc.data()
                        })
                    }
                })

                setOrcamentos(listaOrcamentos);
                setCarregando(0)
            });

        } else {
            firebase.firestore().collection('orcamentos').get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    if (doc.data().codigo.indexOf(pesquisa) >= 0) {
                        listaOrcamentos.push({
                            id: doc.id,
                            ...doc.data()
                        })
                    }
                })

                setOrcamentos(listaOrcamentos);
                setCarregando(0)
            });
        }
    }, [listaOrcamentos, match.params.parametro, pesquisa, usuarioEmail]);

    return(
        <>
        <Navbar />
        {
                carregando ? <div className="row mt-5"><div class="spinner-border text-danger mx-auto" role="status"></div></div>
                :
                <div className="body-content col-sm-12 col-md-8 mx-auto datascreen">
                <div className="row p-3 mx-auto">
                <input onChange={(e) => setPesquisa(e.target.value)} type="text" 
                className="form-control mx-auto search col-sm-8 text-center" placeholder="Pesquisar Oramento pelo ttulo..." />
            </div>
            <div className="row exibicao mx-2 text-center">
                {orcamentos.map(item => <EventoOrcamento key={item.id} id={item.id}
                    codigo={item.codigo} cliente={item.cliente} valor={item.Total} dia={item.data} usuario={item.usuario} />)}
            </div>
            </div>
            }
        <Footer />
        </>
    )
}

export default VisualizarEventosOrcamentos;