import React, { useState, useEffect } from 'react';
import './recebimento-detalhes.css';
import { Link, Redirect } from 'react-router-dom';
import firebase from '../../config/firebase';
import { useSelector } from 'react-redux';
import Navbar from '../../components/navbar/';
import Footer from '../../components/footer/';
import ReactToExcel from 'react-html-table-to-excel'

function RecebimentoDetalhes(props) {

    const [recebimento, setRecebimento] = useState({});
    const [urlFoto1, setFoto1] = useState();
    const [urlFoto2, setFoto2] = useState();
    const [urlFoto3, setFoto3] = useState();
    const [urlFoto4, setFoto4] = useState();
    const [urlFoto5, setFoto5] = useState();
    const usuarioLogado = useSelector(state => state.usuarioEmail);
    const [carregando, setCarregando] = useState(1);
    const [excluido, setExcluido] = useState(0);

    function remover() {
        firebase.firestore().collection('recebimentos').doc(props.match.params.id).delete().then(() => {
            setExcluido(1)
        })
    }

    useEffect(() => {
        if (carregando) {
            firebase.firestore().collection('recebimentos').doc(props.match.params.id).get().then(resultado => {
                setRecebimento(resultado.data())
                firebase.firestore().collection('recebimentos').doc(props.match.params.id).update('visualizacoes', resultado.data().visualizacoes + 1)
                firebase.storage().ref(`recebimentos/${resultado.data().foto1}`).getDownloadURL().then(url => {
                    setFoto1(url)
                });
                firebase.storage().ref(`recebimentos/${resultado.data().foto2}`).getDownloadURL().then(url => {
                    setFoto2(url)
                });
                firebase.storage().ref(`recebimentos/${resultado.data().foto3}`).getDownloadURL().then(url => {
                    setFoto3(url)
                });
                firebase.storage().ref(`recebimentos/${resultado.data().foto4}`).getDownloadURL().then(url => {
                    setFoto4(url)
                });
                firebase.storage().ref(`recebimentos/${resultado.data().foto5}`).getDownloadURL().then(url => {
                    setFoto5(url)
                });
                setCarregando(0)
            });
        }
        else {
            firebase.storage().ref(`recebimentos/${recebimento.foto1}`).getDownloadURL().then(url => {
                setFoto1(url)
            });
            firebase.storage().ref(`recebimentos/${recebimento.foto2}`).getDownloadURL().then(url => {
                setFoto2(url)
            });
            firebase.storage().ref(`recebimentos/${recebimento.foto3}`).getDownloadURL().then(url => {
                setFoto3(url)
            });
            firebase.storage().ref(`recebimentos/${recebimento.foto4}`).getDownloadURL().then(url => {
                setFoto4(url)
            });
            firebase.storage().ref(`recebimentos/${recebimento.foto5}`).getDownloadURL().then(url => {
                setFoto5(url)
            });
            setCarregando(0)
        }
    }, [carregando, props.match.params.id, recebimento.foto1, recebimento.foto2, recebimento.foto3, recebimento.foto4, recebimento.foto5])

    return (
        <>
            <Navbar />

            {excluido ? <Redirect to='/' /> : null}

            {carregando ? <div className="detalhes row mt-5"><div class="spinner-border text-danger mx-auto" role="status"></div></div>
                :
                <>
                <div className="body-content mx-auto form col-md-10 col-sm-12 p-3">
                    <div id="demo" className="mx-auto col-md-10 col-sm-12 box-info p-3 carousel" >

                        <ul className="carousel-indicators">
                            <li data-target="#demo" data-slide-to="0" class="active"></li>
                            <li data-target="#demo" data-slide-to="1"></li>
                            <li data-target="#demo" data-slide-to="2"></li>
                        </ul>

                        <div className="mx-auto carousel-inner">
                            { urlFoto1 ?
                                <div className="mx-auto carousel-item active">
                                <img src={urlFoto1} alt="Foto 1" />
                            </div>
                            : null}
                            { urlFoto2 ?
                                <div className="mx-auto carousel-item">
                                <img src={urlFoto2} alt="Foto 2" />
                            </div>
                            : null}
                            { urlFoto3 ?
                                <div className="mx-auto carousel-item">
                                <img src={urlFoto3} alt="Foto 3" />
                            </div>
                            : null}
                            { urlFoto4 ?
                                <div className="mx-auto carousel-item">
                                <img src={urlFoto4} alt="Foto 4" />
                            </div>
                            : null}
                            { urlFoto5 ?
                                <div className="mx-auto carousel-item">
                                <img src={urlFoto5} alt="Foto 5" />
                            </div>
                            : null}
                        </div>
                        <a class="carousel-control-prev" href="#demo" data-slide="prev">
                            <span class="carousel-control-prev-icon"></span>
                        </a>
                        <a class="carousel-control-next" href="#demo" data-slide="next">
                            <span class="carousel-control-next-icon"></span>
                        </a>
                    </div>
                    <div className="container-fluid">
                        <div>
                            <div className="row">
                                <h3 className="mx-auto mt-5 titulo"><strong>{recebimento.fornecedor}</strong></h3>
                            </div>

                            <div className="row mt-5 d-flex justify-content-around ">
                                <div className="col-md-3 col-sm-12 box-info p-3 my-2">
                                    <i className="fas fa-ticket-alt fa-2x"></i>
                                    <h5><strong>Responsavel</strong></h5>
                                    <span className="mt-3">{recebimento.responsavel}</span>
                                </div>

                                <div className="col-md-3 col-sm-12 box-info p-3 my-2">
                                    <i className="fas fa-calendar-alt fa-2x"></i>
                                    <h5><strong>Data</strong></h5>
                                    <span className="mt-3">{recebimento.data}</span>
                                </div>

                                <div className="col-md-3 col-sm-12 box-info p-3 my-2">
                                    <i className="fas fa-clock fa-2x"></i>
                                    <h5><strong>Numero da Nota</strong></h5>
                                    <span className="mt-3">{recebimento.nota}</span>
                                </div>
                            </div>

                            <div className="row box-detalhes mt-5">
                                <div className="col-12 text-center">
                                    <h5><strong>Detalhes do Recebimento</strong></h5>
                                </div>
                                <div className="col-12 text-center">
                                    <p>{recebimento.detalhes}</p>
                                </div>
                            </div>
                            {
                                usuarioLogado === recebimento.usuario ?
                                <>
                                <div className="col-12 semDeco centro">
                                    <Link className="col-sm-12 semDeco col-md-6" 
                                    to={`/editarrecebimento/${props.match.params.id}`}>
                                        <button type="button" 
                                    className="text-white semDeco btn btn-lg btn-block mt-3 mb-1 btn-success">
                                    <i className="fas text-white fa-pen-square fa-1x mr-1"></i>
                                          Editar</button></Link>
                                    <button  type="button" onClick={remover} 
                                    className="col-sm-12 text-white col-md-6 btn btn-lg btn-block mt-3 mb-1 btn-danger">
                                    <i class="fas fa-trash fa-1x mr-1"></i>
                                        Remover</button>
                                </div>
                                </>
                                    : ''
                            }
                        </div>
                    </div>
<table id="table-to-xls" hidden>
<tr>
        <td>Foto 1</td>
        <td>Foto 2</td>
        <td>Foto 3</td>
        <td>Foto 4</td>
        <td>Foto 5</td>
    </tr>
    <tr>
        <td>{urlFoto1 ? urlFoto1 : "Sem Imagem"}</td>
        <td>{urlFoto2 ? urlFoto2 : "Sem Imagem"}</td>
        <td>{urlFoto3 ? urlFoto3 : "Sem Imagem"}</td>
        <td>{urlFoto4 ? urlFoto4 : "Sem Imagem"}</td>
        <td>{urlFoto5 ? urlFoto5 : "Sem Imagem"}</td>
    </tr>
    <tr>
        <td>RESPONSAVEL</td>
        <td>DATA</td>
        <td>NOTA</td>
        <td>DETALHES</td>
        <td>URL FOTO</td>
    </tr>
    <tr>
        <td>{recebimento.responsavel}</td>
        <td>{recebimento.data}</td>
        <td>{recebimento.nota}</td>
        <td>{recebimento.detalhes}</td>
        <td>{urlFoto1}</td>
    </tr>
</table>
<ReactToExcel
    className="col-md-12 col-sm-12 box-info  p-3 my-2" table="table-to-xls" filename={recebimento.nota + "-" + recebimento.data}
    sheet={recebimento.nota + "-" + recebimento.data} buttonText="EXPORTAR PARA EXCEL"
/>
</div>
</>
            }
           <Footer />
        </>
    )
}

export default RecebimentoDetalhes;