import React, { useState, useEffect } from 'react';
import './v-e-funcionarios.css';
import Navbar from '../../components/navbar/';
import Footer from '../../components/footer/';
import { useSelector } from 'react-redux';
import firebase from '../../config/firebase';
import EventoFuncionario from '../evento-card-funcionario'

function VisualizarEventosFuncionarios({ match }) {

    const [funcionarios, setFuncionarios] = useState([]);
    const [pesquisa, setPesquisa] = useState('');
    const usuarioEmail = useSelector(state => state.usuarioEmail);
    const [carregando, setCarregando] = useState(1)
    let listaFuncionarios = [];

    useEffect(() => {

        if (match.params.parametro) {
            firebase.firestore().collection('funcionarios').where('usuario', '==', usuarioEmail).get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    if (doc.data().nome.indexOf(pesquisa) >= 0) {
                        listaFuncionarios.push({
                            id: doc.id,
                            ...doc.data()
                        })
                    }
                })

                setFuncionarios(listaFuncionarios);
                setCarregando(0)
            });

        } else {
            firebase.firestore().collection('funcionarios').get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    if (doc.data().nome.indexOf(pesquisa) >= 0) {
                        listaFuncionarios.push({
                            id: doc.id,
                            ...doc.data()
                        })
                    }
                })

                setFuncionarios(listaFuncionarios);
                setCarregando(0)
            });
        }
    }, [listaFuncionarios, match.params.parametro, pesquisa, usuarioEmail]);

    return (
        <>
            <Navbar />
            {
                carregando ? <div className="row mt-5"><div class="spinner-border text-danger mx-auto" role="status"></div></div>
                    :
                    <div className="body-content col-sm-12 col-md-8 mx-auto datascreen">
                        <div className="row p-3 mx-auto">
                            <input onChange={(e) => setPesquisa(e.target.value)} type="text"
                                className="search mx-auto col-sm-8 form-control text-center" placeholder="Pesquisar Funcionrio pelo nome..." />
                        </div>
                        <div className="row exibicao mx-2 text-center">
                            {funcionarios.map(item => <EventoFuncionario key={item.id} id={item.id}
                                nome={item.nome} setor={item.setor} telefone={item.telefone} detalhes={item.detalhes} usuario={item.usuarioEmail} />)}
                        </div>
                    </div>
            }
            <Footer />
        </>
    )
}

export default VisualizarEventosFuncionarios;