import React, { useState, useEffect } from 'react';
import './ver-qualidade.css';
import Navbar from '../../components/navbar/';
import Footer from '../../components/footer/';
import { useSelector } from 'react-redux';
import firebase from '../../config/firebase';
import EventoQualidade from '../../components/evento-card-qualidade/'

function VisualizarEventosQualidade({ match }) {

    const [pesquisa, setPesquisa] = useState('');
    const [registros, setRegistros] = useState([]);
    const [carregando , setCarregando] = useState(1)
    let listaRegistros = [];
    const usuarioEmail = useSelector(state => state.usuarioEmail);

    useEffect(() => {

        if (match.params.parametro) {
            firebase.firestore().collection('qualidade').where('usuario', '==', usuarioEmail).get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    if (doc.data().titulo.indexOf(pesquisa) >= 0) {
                        listaRegistros.push({
                            id: doc.id,
                            ...doc.data()
                        })
                    }
                },)

                setRegistros(listaRegistros);
                setCarregando(0)
            });

        } else {
            firebase.firestore().collection('qualidade').get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    if (doc.data().titulo.indexOf(pesquisa) >= 0) {
                        listaRegistros.push({
                            id: doc.id,
                            ...doc.data()
                        })
                    }
                })

                setRegistros(listaRegistros);
                setCarregando(0)
            });
        }
    }, [listaRegistros, match.params.parametro, pesquisa, usuarioEmail]);

    return (
        <>
            <Navbar />

            {
                carregando ? <div className="row mt-5"><div class="spinner-border text-danger mx-auto" role="status"></div></div>
                    :
                    <div className="body-content col-sm-12 col-md-8 mx-auto datascreen">
                        <div className="row p-3 mx-auto">
                            <input onChange={(e) => setPesquisa(e.target.value)} type="text"
                             className="search col-sm-8 mx-auto form-control text-center" placeholder="Pesquisar No conformidades pelo ttulo..." />
                        </div>
                        <div className="row exibicao mx-2 text-center">
                            {registros.map(item => <EventoQualidade key={item.id} id={item.id} img={item.foto1} data={item.data}
                                titulo={item.titulo} causancf={item.causa} />)}
                        </div>
                    </div>
            }
        <Footer />
        </>
    )
}

export default VisualizarEventosQualidade;