import './orcamento-cadastro.css';
import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import Navbar from '../../components/navbar/';
import Footer from '../../components/footer/';
import Form from '../../components/form-cadastro-orcamento/';
import firebase from '../../config/firebase';

function OrcamentoCadastro() {
    let width = localStorage.getItem('width')
    const [orientation, setOrientation] = useState()
    const [acesso, setAcesso] = useState('')
    const usuarioEmail = useSelector(state => state.usuarioEmail);
    const db = firebase.firestore();


    //verifica se a tela esta em landscape ou no, e atualiza a variavel
    // de estado orientation que renderiza a tela
    // eslint-disable-next-line 
    {
        window.addEventListener('orientationchange', function () {
            switch (window.orientation) {
                case -90: setOrientation(0); break; case 90: setOrientation(1);
                    break; default: setOrientation(0); break;
            }
        });
    }

    // verifica se o usuario est logado
    useEffect(() => {
        let usersRef = db.collection('users');
        // eslint-disable-next-line
        let query = usersRef.where('usuarioEmail', '==', usuarioEmail).get()
          .then(snapshot => {
            if (snapshot.empty) {
              return;
            }
        
            snapshot.forEach(doc => {
              const {acesso} = doc.data()
              setAcesso(acesso)
            });


          })
          .catch(err => {
            console.log('Error getting documents', err);
          });
    }, [db, usuarioEmail])

    return (
        <>
            <Navbar />
            {parseInt(acesso) === 3 ?
                <>
                    {width < 600 ?
                        <>
                            {orientation === 1 ?
                                <Form />
                                :
                                <div className="body-content col-12 mx-auto aviso">
                                    <div className="flex-box col-sm-12 col-md-12">
                                        <h1 className="centralizado">Ola!</h1>
                                        <h3 className="mx-auto mt-2 centralizado col-sm-12  font-weight-bold">Por favor, mude sua orientao de tela para landscape (deitado).</h3>
                                    </div>
                                </div>}
                        </>

                        :
                        <Form />
                    }

                </> :
                <div className="mt-5 text-center"><h1>Voce no tem autorizao para cadastrar</h1></div>}
            <Footer />
        </>
    )
}

export default OrcamentoCadastro;