import React, { useState, useEffect } from 'react';
import './pcp.css';
import Navbar from '../navbar';
import Footer from '../footer';
import { useSelector } from 'react-redux';
import firebase from '../../config/firebase';
import EventoCardPcp from '../evento-card-pcp'

function VisualizarEventosXcite({ match }){

    const [pcp, setPcp] = useState([]);
    const [pesquisa, setPesquisa] = useState('');
    const usuarioEmail = useSelector(state => state.usuarioEmail);
    const [carregando , setCarregando] = useState(1)
    let ListaPcp = []; 

    useEffect(() => {

        if (match.params.parametro) {
            firebase.firestore().collection('Xcite').where('usuarioEmail', '==', usuarioEmail).get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    if (doc.data().data.indexOf(pesquisa) >= 0) {
                        ListaPcp.push({
                            id: doc.id,
                            ...doc.data()
                        })
                    }
                })

                setPcp(ListaPcp);
                setCarregando(0)
            });

        } else {
            firebase.firestore().collection('Xcite').get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    if (doc.data().data.indexOf(pesquisa) >= 0) {
                        ListaPcp.push({
                            id: doc.id,
                            ...doc.data()
                        })
                    }
                })

                setPcp(ListaPcp);
                setCarregando(0)
            });
        }
    } , [ListaPcp, match.params.parametro, pesquisa, usuarioEmail]);

    return(
        <>
        <Navbar />
        {
                carregando ? <div className="row mt-5"><div class="spinner-border text-danger mx-auto" role="status"></div></div>
                : 
            <div className="col-sm-12 col-md-8 mx-auto datascreen body-content ml-0">
            <div className="row p-3 col-sm-12 search">
                <input onChange={(e) => setPesquisa(e.target.value)} type="text" 
                className="form-control search col-sm-8 text-center" 
                placeholder="Pesquisar data..." />
            </div>
            <div className="row exibicao mx-2 text-center">
                {pcp.map(item => <EventoCardPcp key={item.id} id={item.id}
                   maquina={item.maquina} data={item.data} usuario={item.usuarioEmail} />)}
            </div>
            </div>
            }
        <Footer />
        </>
    )
}

export default VisualizarEventosXcite;