import React, { useState, useEffect } from 'react';
import './fornecedor.css';
import { Link, Redirect } from 'react-router-dom';
import firebase from '../../config/firebase';
import { useSelector } from 'react-redux';
import Navbar from '../../components/navbar/';
import Footer from '../../components/footer/';

function FornecedorDetalhes(props) {
    const [fornecedores, setFornecedores] = useState({});
    const usuarioLogado = useSelector(state => state.usuarioEmail);
    const [carregando, setCarregando] = useState(1);
    const [excluido, setExcluido] = useState(0);

    function remover(){
        firebase.firestore().collection('fornecedores').doc(props.match.params.id).delete().then(() => {
            setExcluido(1)
        })
    }

    useEffect(() => {
        if(carregando){
        firebase.firestore().collection('fornecedores').doc(props.match.params.id).get().then(resultado => {
            setFornecedores(resultado.data())
            setCarregando(0)
        })
}})
 return(
<>
<Navbar />

{excluido ? <Redirect to='/' /> : null}

{ carregando ? <div className="row mt-5"><div class="spinner-border detalhes text-danger mx-auto" role="status"></div></div>
:
<div>
<div className="body-content mx-auto form col-md-10 col-sm-12 p-3">
    <div>
        <div className="row">
            <h3 className="mx-auto mt-5 titulo"><strong>{fornecedores.nome}</strong></h3>
        </div>

        <div className="row mt-5 d-flex justify-content-around ">
            <div className="col-md-3 col-sm-12 box-info p-3 my-2">
                <i className="fas fa-ticket-alt fa-2x"></i>
                <h5><strong>CPF OU CNPJ</strong></h5>
            <span className="mt-3">{fornecedores.cnpj}</span>
            </div>

            <div className="col-md-3 col-sm-12 box-info p-3 my-2">
                <i className="fas fa-ticket-alt fa-2x"></i>
                <h5><strong>Telefone</strong></h5>
            <span className="mt-3">{fornecedores.telefone}</span>
            </div>

            <div className="col-md-3 col-sm-12 box-info p-3 my-2">
                <i className="fas fa-ticket-alt fa-2x"></i>
                <h5><strong>Detalhes</strong></h5>
                <span className="mt-3">{fornecedores.detalhes}</span>
            </div>
        </ div>
        {
                                usuarioLogado === fornecedores.usuarioEmail ?
                                <>
                                <div className="col-12 semDeco centro">
                                    <Link className="col-sm-12 semDeco col-md-6" 
                                    to={`/editarfornecedores/${props.match.params.id}`}>
                                        <button type="button"
                                    className="text-white semDeco btn btn-lg btn-block mt-3 mb-1 btn-success">
                                    <i className="fas text-white fa-pen-square fa-1x mr-1"></i>
                                          Editar</button></Link>
                                    <button  type="button" onClick={remover} 
                                    className="col-sm-12 text-white col-md-6 btn btn-lg btn-block mt-3 mb-1 btn-danger">
                                    <i class="fas fa-trash fa-1x mr-1"></i>
                                        Remover</button>
                                </div>
                                </>
                                    : ''
                            }
    </div>
</div>/
</div>
}
<Footer />
</>
)
}

export default FornecedorDetalhes;