import React, { useState, useEffect } from 'react';
import './v-e-clientes.css';
import Navbar from '../../components/navbar/';
import Footer from '../../components/footer/';
import { useSelector } from 'react-redux';
import firebase from '../../config/firebase';
import EventoCliente from '../evento-card-clientes'

function VisualizarEventosClientes({ match }) {

    const [clientes, setClientes] = useState([]);
    const [pesquisa, setPesquisa] = useState('');
    const usuarioEmail = useSelector(state => state.usuarioEmail);
    const [carregando, setCarregando] = useState(1)
    let listaClientes = [];

    useEffect(() => {

        if (match.params.parametro) {
            firebase.firestore().collection('clientes').where('usuario', '==', usuarioEmail).get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    if (doc.data().nome.indexOf(pesquisa) >= 0) {
                        listaClientes.push({
                            id: doc.id,
                            ...doc.data()
                        })
                    }
                })

                setClientes(listaClientes);
                setCarregando(0)
            });

        } else {
            firebase.firestore().collection('clientes').get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    if (doc.data().nome.indexOf(pesquisa) >= 0) {
                        listaClientes.push({
                            id: doc.id,
                            ...doc.data()
                        })
                    }
                })

                setClientes(listaClientes);
                setCarregando(0)
            });
        }
    }, [listaClientes, match.params.parametro, pesquisa, usuarioEmail]);

    return (
        <>
            <Navbar />
            {
                carregando ? <div className="row mt-5"><div class="spinner-border text-danger mx-auto" role="status"></div></div>
                    :
                    <div className="body-content col-sm-12 col-md-8 mx-auto datascreen">
                        <div className="row p-3 mx-auto">
                            <input onChange={(e) => setPesquisa(e.target.value)} type="text"
                                className="form-control mx-auto search col-sm-8 text-center" placeholder="Pesquisar cliente pelo nome..." />
                        </div>
                        <div className="row exibicao mx-2 text-center">
                            {clientes.map(item => <EventoCliente key={item.id} id={item.id}
                                nome={item.nome} cnpjoucpf={item.cnpjoucpf} telefone={item.telefone} detalhes={item.detalhes} usuario={item.usuarioEmail} />)}
                        </div>
                    </div>
            }
            <Footer />
        </>
    )
}

export default VisualizarEventosClientes;