import React, { useState, useEffect } from 'react';
import './v-e-funcionarios.css';
import Navbar from '../navbar';
import Footer from '../footer';
import { useSelector } from 'react-redux';
import firebase from '../../config/firebase';
import EventoMp from '../evento-card-mp'

function VisualizarEventosMp({ match }) {

    const [materiaprima, setMateriaPrima] = useState([]);
    const [pesquisa, setPesquisa] = useState('');
    const usuarioEmail = useSelector(state => state.usuarioEmail);
    const [carregando, setCarregando] = useState(1)
    let ListaMateriaPrima = [];

    useEffect(() => {

        if (match.params.parametro) {
            firebase.firestore().collection('materiaprima').where('usuario', '==', usuarioEmail).get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    if (doc.data().nome.indexOf(pesquisa) >= 0) {
                        ListaMateriaPrima.push({
                            id: doc.id,
                            ...doc.data()
                        })
                    }
                })

                setMateriaPrima(ListaMateriaPrima);
                setCarregando(0)
            });

        } else {
            firebase.firestore().collection('materiaprima').get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    if (doc.data().nome.indexOf(pesquisa) >= 0) {
                        ListaMateriaPrima.push({
                            id: doc.id,
                            ...doc.data()
                        })
                    }
                })

                setMateriaPrima(ListaMateriaPrima);
                setCarregando(0)
            });
        }
    }, [ListaMateriaPrima, match.params.parametro, pesquisa, usuarioEmail]);

    return (
        <>
            <Navbar />
            {
                carregando ? <div className="row mt-5"><div class="spinner-border text-danger mx-auto" role="status"></div></div>
                    :
                    <div className="body-content col-sm-12 col-md-8 mx-auto datascreen">
                        <div className="row p-3 mx-auto">
                            <input onChange={(e) => setPesquisa(e.target.value)} type="text"
                                className="form-control mx-auto search col-sm-8 text-center" placeholder="Pesquisar Matria Prima pelo nome..." />
                        </div>
                        <div className="row exibicao mx-2 text-center">
                            {materiaprima.map(item => <EventoMp key={item.id} id={item.id}
                                nome={item.nome} valor={item.valor} detalhes={item.detalhes} usuario={item.usuarioEmail} />)}
                        </div>
                    </div>
            }
            <Footer />
        </>
    )
}

export default VisualizarEventosMp;