import React, { useState, useEffect } from 'react';
import './pcp.css';
import { Link, Redirect } from 'react-router-dom';
import firebase from '../../config/firebase';
import { useSelector } from 'react-redux';
import Navbar from '../../components/navbar';
import Footer from '../../components/footer';

function PcpDetalhes(props) {
    const [pcp, setPcp] = useState({});
    const usuarioLogado = useSelector(state => state.usuarioEmail);
    const [carregando, setCarregando] = useState(1);
    const [excluido, setExcluido] = useState(0);

    function remover() {
        if (props.match.params.mq === '01'){
            firebase.firestore().collection('B6520').doc(props.match.params.id).delete().then(() => {
                setExcluido(1)
            })
        }
        else if (props.match.params.mq === '02'){
            firebase.firestore().collection('B4025').doc(props.match.params.id).delete().then(() => {
                setExcluido(1)
            })
        }
        else if (props.match.params.mq === '03'){
            firebase.firestore().collection('Xcite').doc(props.match.params.id).delete().then(() => {
                setExcluido(1)
            })
        }
        else if (props.match.params.mq === '04'){
            firebase.firestore().collection('Xpert').doc(props.match.params.id).delete().then(() => {
                setExcluido(1)
            })
        }
        else if (props.match.params.mq === '05'){
            firebase.firestore().collection('durma').doc(props.match.params.id).delete().then(() => {
                setExcluido(1)
            })
        }
        else {
            alert("ERRO AO DELETAR NO BANCO")
        }
    }

    useEffect(() => {
        if (carregando) {
            if (props.match.params.mq === '01'){
                firebase.firestore().collection('B6520').doc(props.match.params.id).get().then(resultado => {
                    setPcp(resultado.data())
                    setCarregando(0)
                })
            }
            else if (props.match.params.mq === '02'){
                firebase.firestore().collection('B4025').doc(props.match.params.id).get().then(resultado => {
                    setPcp(resultado.data())
                    setCarregando(0)
                })
            }
            else if (props.match.params.mq === '03'){
                firebase.firestore().collection('Xcite').doc(props.match.params.id).get().then(resultado => {
                    setPcp(resultado.data())
                    setCarregando(0)
                })
            }
            else if (props.match.params.mq === '04'){
                firebase.firestore().collection('Xpert').doc(props.match.params.id).get().then(resultado => {
                    setPcp(resultado.data())
                    setCarregando(0)
                })
            }
            else if (props.match.params.mq === '05'){
                firebase.firestore().collection('durma').doc(props.match.params.id).get().then(resultado => {
                    setPcp(resultado.data())
                    setCarregando(0)
                })
            }
            else {
                alert("ERRO AO BUSCAR NO BANCO")
            }
        }
    })
    return (
        <>
            <Navbar />

            {excluido ? <Redirect to='/' /> : null}

            {carregando ? <div className="row detalhes mt-5">
                <div class="spinner-border text-danger mx-auto" role="status">
                    </div></div>
                :
                <div className="body-content mx-auto form col-md-10 col-sm-12 p-3">
                    <div className="container-fluid">
                        <div>
                            <div className="row">
                                <h3 className="mx-auto text-center mt-5 titulo">
                                    <strong>Programao da produo</strong><br/>
                                    <strong>{pcp.data}</strong></h3>
                            </div>
                            <table className="font" class="table">
                                <thead class="thead-dark">
                                    <tr>
                                        <th colspan="5" scope="col">Planejador: {pcp.usuario}</th>
                                    </tr>
                                    <tr>
                                        <th scope="col">Maquina</th>
                                        <th scope="col">Horas a produzir</th>
                                        <th scope="col">Horas produzidas</th>
                                        <th scope="col">% Aproveitamento</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <th scope="row">{pcp.maquina}</th>
                                        <td>{pcp.horasaproduzir}</td>
                                        <td>{pcp.horasproduzidas}</td>
                                        <td>{((pcp.horasproduzidas / pcp.horasaproduzir) * 100).toFixed(2)}</td>
                                    </tr>
                                </tbody>
                            </table>

                            {
                                usuarioLogado === pcp.usuario ?
                                    <>
                                        <div className="col-12 semDeco centro">
                                        {pcp.maquina === 'Bysprint 6520' ? 
                                        <>
                                        <Link className="col-sm-12 semDeco col-md-6"
                                        to={`/editarpcp/${props.match.params.id}/01`}>
                                          <button type="button"
                                        className="text-white semDeco btn btn-lg btn-block mt-3 mb-1 btn-success">
                                                <i className="fas text-white fa-pen-square fa-1x mr-1"></i> 
                                        Editar</button></Link>
                                        </>
                                        : null}
                                        {pcp.maquina === 'Bystar 4025' ? 
                                        <>
                                        <Link className="col-sm-12 semDeco col-md-6"
                                        to={`/editarpcp/${props.match.params.id}/02`}>
                                          <button type="button"
                                        className="text-white semDeco btn btn-lg btn-block mt-3 mb-1 btn-success">
                                                <i className="fas text-white fa-pen-square fa-1x mr-1"></i> 
                                        Editar</button></Link>
                                        </>
                                        : null}
                                        {pcp.maquina === 'Xcite' ? 
                                        <>
                                        <Link className="col-sm-12 semDeco col-md-6"
                                        to={`/editarpcp/${props.match.params.id}/03`}>
                                          <button type="button"
                                        className="text-white semDeco btn btn-lg btn-block mt-3 mb-1 btn-success">
                                                <i className="fas text-white fa-pen-square fa-1x mr-1"></i> 
                                        Editar</button></Link>
                                        </>
                                        : null}
                                        {pcp.maquina === 'Xpert' ? 
                                        <>
                                        <Link className="col-sm-12 semDeco col-md-6"
                                        to={`/editarpcp/${props.match.params.id}/04`}>
                                          <button type="button"
                                        className="text-white semDeco btn btn-lg btn-block mt-3 mb-1 btn-success">
                                                <i className="fas text-white fa-pen-square fa-1x mr-1"></i> 
                                        Editar</button></Link>
                                        </>
                                        : null}
                                        {pcp.maquina === 'Dobradeira Durma' ? 
                                        <>
                                        <Link className="col-sm-12 semDeco col-md-6"
                                        to={`/editarpcp/${props.match.params.id}/05`}>
                                          <button type="button"
                                        className="text-white semDeco btn btn-lg btn-block mt-3 mb-1 btn-success">
                                                <i className="fas text-white fa-pen-square fa-1x mr-1"></i> 
                                        Editar</button></Link>
                                        </>
                                        : null}
                                            <button type="button" onClick={remover}
                                                className="col-sm-12 text-white col-md-6 btn btn-lg btn-block mt-3 mb-1 btn-danger">
                                                <i class="fas fa-trash fa-1x mr-1"></i>
                                        Remover</button>
                                        </div>
                                    </>
                                    : ''
                            }
                        </div>
                    </div>
</div>
            }
            <Footer />
        </>
    )
}

export default PcpDetalhes;