import React, { useState, useEffect } from 'react';
import './setordetalhes.css';
import { Redirect } from 'react-router-dom';
import firebase from '../../config/firebase';
import { useSelector } from 'react-redux';
import Navbar from '../../components/navbar';
import Footer from '../../components/footer';

function SetorDetalhes(props) {
    const [setores, setSetores] = useState({});
    const usuarioLogado = useSelector(state => state.usuarioEmail);
    const [carregando, setCarregando] = useState(1);
    const [excluido, setExcluido] = useState(0);

    function remover(){
        firebase.firestore().collection('setores').doc(props.match.params.id).delete().then(() => {
            setExcluido(1)
        })
    }

    useEffect(() => {
        if(carregando){
        firebase.firestore().collection('setores').doc(props.match.params.id).get().then(resultado => {
            setSetores(resultado.data())
            setCarregando(0)
        })
}})
 return(
<>
<Navbar />

{excluido ? <Redirect to='/' /> : null}

{ carregando ? <div className="row detalhes mt-5"><div class="spinner-border text-danger mx-auto" role="status"></div></div>
:
<div className="body-content mx-auto form col-md-10 col-sm-12 p-3">
<div className="container-fluid">
    <div>
        <div className="row">
            <h3 className="mx-auto mt-5 titulo"><strong>{setores.nome}</strong></h3>
        </div>

        <div className="row mt-5 d-flex justify-content-around ">
            <div className="col-md-3 col-sm-12 box-info p-3 my-2">
                <i className="fas fa-ticket-alt fa-2x"></i>
                <h5><strong>Detalhes</strong></h5>
                <span className="mt-3">{setores.detalhes}</span>
            </div>
        </ div>

                {
                    usuarioLogado === setores.usuarioEmail ?
                        <button type="button" onClick={remover} className="btn btn-lg btn-block mt-3 mb-1 btn-cadastro">
                            Remover setor</button>
                        : ''
                }
    </div>
</div>
</div>
}
<Footer />
</>
)
}

export default SetorDetalhes;