import React, { useState, useEffect } from 'react';
import './qualidade-detalhes.css';
import { Redirect } from 'react-router-dom';
import firebase from '../../config/firebase';
import { useSelector } from 'react-redux';
import Navbar from '../../components/navbar/';
import Footer from '../../components/footer/';
import ReactToExcel from 'react-html-table-to-excel';

function QualidadeDetalhes(props) {
    const [qualidade, setQualidade] = useState({});
    const [urlFoto1, setFoto1] = useState();
    const [urlFoto2, setFoto2] = useState();
    const [urlFoto3, setFoto3] = useState();
    const [urlFoto4, setFoto4] = useState();
    const [urlFoto5, setFoto5] = useState();
    const usuarioLogado = useSelector(state => state.usuarioEmail);
    const [carregando, setCarregando] = useState(1);
    const [excluido, setExcluido] = useState(0);

    function remover() {
        firebase.firestore().collection('qualidade').doc(props.match.params.id).delete().then(() => {
            setExcluido(1)
        })
    }

    useEffect(() => {
        if (carregando) {
            firebase.firestore().collection('qualidade').doc(props.match.params.id).get().then(resultado => {
                setQualidade(resultado.data())
                firebase.storage().ref(`qualidade/${resultado.data().foto1}`).getDownloadURL().then(url => {
                    setFoto1(url)
                });
                firebase.storage().ref(`qualidade/${resultado.data().foto2}`).getDownloadURL().then(url => {
                    setFoto2(url)
                });
                firebase.storage().ref(`qualidade/${resultado.data().foto3}`).getDownloadURL().then(url => {
                    setFoto3(url)
                });
                firebase.storage().ref(`qualidade/${resultado.data().foto4}`).getDownloadURL().then(url => {
                    setFoto4(url)
                });
                firebase.storage().ref(`qualidade/${resultado.data().foto5}`).getDownloadURL().then(url => {
                    setFoto5(url)
                });
                setCarregando(0)
                console.log(urlFoto1)
            });
        }
        else {
            firebase.storage().ref(`qualidade/${qualidade.foto1}`).getDownloadURL().then(url => {
                setFoto1(url)
            });
            firebase.storage().ref(`qualidade/${qualidade.foto2}`).getDownloadURL().then(url => {
                setFoto2(url)
            });
            firebase.storage().ref(`qualidade/${qualidade.foto3}`).getDownloadURL().then(url => {
                setFoto3(url)
            });
            firebase.storage().ref(`qualidade/${qualidade.foto4}`).getDownloadURL().then(url => {
                setFoto4(url)
            });
            firebase.storage().ref(`qualidade/${qualidade.foto5}`).getDownloadURL().then(url => {
                setFoto5(url)
            });
            setCarregando(0)
        }
    }, [carregando, props.match.params.id, qualidade.foto1, qualidade.foto2, qualidade.foto3, qualidade.foto4, qualidade.foto5, urlFoto1])

    return (
        <>
            <Navbar />

            {excluido ? <Redirect to='/' /> : null}

            {carregando ? <div className="row detalhes mt-5"><div class="spinner-border text-danger mx-auto" role="status"></div></div>
                :
                <div className="body-content mx-auto form col-md-10 col-sm-12 p-3">
                    <div id="demo" className="mx-auto detalhes col-md-10 col-sm-12 centro box-info p-3 carousel" >

                        <ul className="carousel-indicators">
                            <li data-target="#demo" data-slide-to="0" class="active"></li>
                            <li data-target="#demo" data-slide-to="1"></li>
                            <li data-target="#demo" data-slide-to="2"></li>
                        </ul>

                        <div className="mx-auto centro carousel-inner">
                            {urlFoto1 ?
                                <div className="mx-auto carousel-item active">
                                    <img src={urlFoto1} alt="Foto 1" />
                                </div>
                                : null}
                            {urlFoto2 ?
                                <div className="mx-auto carousel-item">
                                    <img src={urlFoto2} alt="Foto 2" />
                                </div>
                                : null}
                            {urlFoto3 ?
                                <div className="mx-auto carousel-item">
                                    <img src={urlFoto3} alt="Foto 3" />
                                </div>
                                : null}
                            {urlFoto4 ?
                                <div className="mx-auto carousel-item">
                                    <img src={urlFoto4} alt="Foto 4" />
                                </div>
                                : null}
                            {urlFoto5 ?
                                <div className="mx-auto carousel-item">
                                    <img src={urlFoto5} alt="Foto 5" />
                                </div>
                                : null}
                        </div>
                        <a class="carousel-control-prev" href="#demo" data-slide="prev">
                            <span class="carousel-control-prev-icon"></span>
                        </a>
                        <a class="carousel-control-next" href="#demo" data-slide="next">
                            <span class="carousel-control-next-icon"></span>
                        </a>
                    </div>

                    <div className="container-fluid mx-auto form col-md-10 col-sm-12">
                        <div>
                            <div className="row">
                                <h3 className="mx-auto form mt-5 titulo"><strong>{qualidade.titulo}</strong></h3>
                            </div>

                            <div className="row mt-5 d-flex  justify-content-around ">
                                <div className="col-md-3 col-sm-12 box-info p-3 my-2">
                                    <i className="fas fa-ticket-alt fa-2x"></i>
                                    <h5><strong>Causa NCF</strong></h5>
                                    <span className="mt-3">{qualidade.causa}</span>
                                </div>

                                <div className="col-md-3 col-sm-12 box-info p-3 my-2">
                                    <i className="fas fa-ticket-alt fa-2x"></i>
                                    <h5><strong>Quant. da Of</strong></h5>
                                    <span className="mt-3">{qualidade.quantOf}</span>
                                </div>

                                <div className="col-md-3 col-sm-12 box-info p-3 my-2">
                                    <i className="fas fa-ticket-alt fa-2x"></i>
                                    <h5><strong>Quant. no conforme</strong></h5>
                                    <span className="mt-3">{qualidade.quantNaoConforme}</span>
                                </div>
                            </ div>
                            <div className="row mt-5 d-flex justify-content-around ">
                                <div className="col-md-3 col-sm-12 box-info p-3 my-2">
                                    <i className="fas fa-clock fa-2x"></i>
                                    <h5><strong>Numero da of</strong></h5>
                                    <span className="mt-3">{qualidade.numeroOf}</span>
                                </div>

                                <div className="col-md-3 col-sm-12 box-info p-3 my-2">
                                    <i className="fas fa-clock fa-2x"></i>
                                    <h5><strong>Hora</strong></h5>
                                    <span className="mt-3">{qualidade.hora}</span>
                                </div>

                                <div className="col-md-3 col-sm-12 box-info p-3 my-2">
                                    <i className="fas fa-calendar-alt fa-2x"></i>
                                    <h5><strong>Data</strong></h5>
                                    <span className="mt-3">{qualidade.data}</span>
                                </div>
                            </div>

                            <div className="row form box-detalhes mt-5">
                                <div className="col-12 text-center">
                                    <h5><strong>Detalhes da no conformidade</strong></h5>
                                </div>
                                <div className="col-12 text-center">
                                    <p>{qualidade.detalhes}</p>
                                </div>
                            </div>

                            <div className="row form box-detalhes mt-5">
                                <div className="col-12 text-center">
                                    <h5><strong>Soluo da no conformidade</strong></h5>
                                </div>
                                <div className="col-12 text-center">
                                    <p>{qualidade.solucao}</p>
                                </div>
                            </div>

                            {
                                usuarioLogado === qualidade.usuario ?
                                    <button type="button" onClick={remover} className="btn btn-lg btn-block mt-3 mb-1 btn-cadastro">
                                        Remover No conformidade</button>
                                    : ''
                            }
                            <table hidden id="table-to-xls" class="table">
                                <tr>
                                    <td>Foto 1</td>
                                    <td>Foto 2</td>
                                    <td>Foto 3</td>
                                    <td>Foto 4</td>
                                    <td>Foto 5</td>
                                </tr>
                                <tr>
                                    <td>{urlFoto1 ? urlFoto1 : "Sem Imagem"}</td>
                                    <td>{urlFoto2 ? urlFoto2 : "Sem Imagem"}</td>
                                    <td>{urlFoto3 ? urlFoto3 : "Sem Imagem"}</td>
                                    <td>{urlFoto4 ? urlFoto4 : "Sem Imagem"}</td>
                                    <td>{urlFoto5 ? urlFoto5 : "Sem Imagem"}</td>
                                </tr>
                                <tr>
                                    <td >TITULO</td>
                                    <td >CAUSA</td>
                                    <td >QUANTIDADE DA OF</td>
                                    <td >QUANTIDADE NO CONFORME</td>
                                    <td >NUMERO OF</td>
                                    <td >HORA</td>
                                    <td >DATA</td>
                                    <td >DETALHES</td>
                                    <td >SOLUO</td>
                                </tr>
                                <tr>
                                    <td>{qualidade.titulo}</td>
                                    <td>{qualidade.causa}</td>
                                    <td>{qualidade.quantOf}</td>
                                    <td>{qualidade.quantNaoConforme}</td>
                                    <td>{qualidade.numeroOf}</td>
                                    <td>{qualidade.hora}</td>
                                    <td>{qualidade.data}</td>
                                    <td>{qualidade.detalhes}</td>
                                    <td>{qualidade.solucao}</td>
                                </tr>
                            </table>
                            <ReactToExcel
                                className="col-md-12 col-sm-12 box-info  p-3 my-2" table="table-to-xls" filename={qualidade.numeroOf + "-" + qualidade.data + "-" + qualidade.detalhes}
                                sheet={qualidade.numeroOf + "-" + qualidade.data + "-" + qualidade.detalhes} buttonText="EXPORTAR PARA EXCEL"
                            />
                        </div>
                    </div>
                </div>
            }
            <Footer />
        </>
    )
}

export default QualidadeDetalhes;