import React, { useState, useEffect } from 'react';
import './ver-recebimentos.css';
import Navbar from '../navbar';
import Footer from '../footer';
import { useSelector } from 'react-redux';
import firebase from '../../config/firebase';
import EventoRecebimento from '../evento-card-recebimento'

function VisualizarEventosRecebimento({ match }) {

    const [pesquisa, setPesquisa] = useState('');
    const [recebimentos, setRecebimentos] = useState([]);
    const [carregando , setCarregando] = useState(1)
    let listaRecebimentos = [];
    const usuarioEmail = useSelector(state => state.usuarioEmail);

    useEffect(() => {

        if (match.params.parametro) {
            firebase.firestore().collection('recebimentos').where('usuario', '==', usuarioEmail).get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    if (doc.data().nota.indexOf(pesquisa) >= 0) {
                        listaRecebimentos.push({
                            id: doc.id,
                            ...doc.data()
                        })
                    }
                })

                setRecebimentos(listaRecebimentos);
                setCarregando(0)
            });

        } else {
            firebase.firestore().collection('recebimentos').get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    if (doc.data().nota.indexOf(pesquisa) >= 0) {
                        listaRecebimentos.push({
                            id: doc.id,
                            ...doc.data()
                        })
                    }
                })

                setRecebimentos(listaRecebimentos);
                setCarregando(0)
            });
        }
    }, [listaRecebimentos, match.params.parametro, pesquisa, usuarioEmail]);

    return (
        <>
            <Navbar />

            {
                carregando ? <div className="row mt-5"><div class="spinner-border text-danger mx-auto" role="status"></div></div>
                :
            <div className="body-content col-sm-12 col-md-8 mx-auto datascreen">
            <div className="row p-3 mx-auto">
                <input onChange={(e) => setPesquisa(e.target.value)}
                 type="text" className="search mx-auto col-sm-8 form-control text-center" 
                 placeholder="Pesquisar Recebimento pelo ttulo..." />
            </div>
            <div className="row exibicao mx-2 text-center ">
                {recebimentos.map(item => <EventoRecebimento key={item.id} id={item.id} img={item.foto1}
                    nota={item.nota} data={item.data} visualizacoes={item.visualizacoes} fornecedor={item.fornecedor} />)}
            </div>
            </div>
            }
        <Footer />
        </>
    )
}

export default VisualizarEventosRecebimento;