import React, { useState } from 'react';
import './usuario-recuperar-senha.css';
import Footer from '../../components/footer/';
import {Redirect} from 'react-router-dom'

import firebase from '../../config/firebase';
import 'firebase/auth';

function UsuarioRecuperarSenha(){

    const [email, setEmail] = useState();
    const [msg, setMsg] = useState();
    const [redirect , setRedirect] = useState(false)

    function recuperarSenha(){
        firebase.auth().sendPasswordResetEmail(email).then(resultado => {
            setMsg('Enviamos um link no seu email para voc redefinir sua senha!');
            setRedirect(true)
        }).catch(erro => {
            setMsg('Verifique se o email est correto!');
        })
    }

    return(
        <>
            <div className="body-content text-center centro">
                {redirect === true ? <Redirect to="/login" /> : null}
                    <div className=" col-sm-12 mt-4 centro form col-md-4">
                        <form className="row centro p-3 col-sm-12">               
                    <h3 className="mb-3 font-weight-bold">Redefinir senha</h3>
                    <input onChange={(e) => setEmail(e.target.value)} type="email" className="form-control my-2" placeholder="Email" />

                    <div className="msg my-4 text-center">
                        <span>{msg}</span>
                    </div>

                    <button onClick={recuperarSenha} type="button" className="btn btn-lg btn-block btn-enviar">Recuperar Senha</button>
                </form>
                </div>
            </div>
            <Footer />
        </>        
    )
}

export default UsuarioRecuperarSenha;