import React, { useState, useEffect } from 'react';
import './carregamento-detalhes.css';
import { Link, Redirect } from 'react-router-dom';
import firebase from '../../config/firebase';
import { useSelector } from 'react-redux';
import Navbar from '../../components/navbar/';
import Footer from '../../components/footer/';
import ReactToExcel from 'react-html-table-to-excel';

function CarregamentoDetalhes(props) {

    const [carregamento, setCarregamento] = useState({});
    const [urlFoto1, setFoto1] = useState();
    const [urlFoto2, setFoto2] = useState();
    const [urlFoto3, setFoto3] = useState();
    const [urlFoto4, setFoto4] = useState();
    const [urlFoto5, setFoto5] = useState();
    const [urlFoto6, setFoto6] = useState();
    const [urlFoto7, setFoto7] = useState();
    const [urlFoto8, setFoto8] = useState();
    const [urlFoto9, setFoto9] = useState();
    const [urlFoto10, setFoto10] = useState();
    const [urlFoto11, setFoto11] = useState();
    const [urlFoto12, setFoto12] = useState();
    const [urlFoto13, setFoto13] = useState();
    const [urlFoto14, setFoto14] = useState();
    const [urlFoto15, setFoto15] = useState();
    const usuarioLogado = useSelector(state => state.usuarioEmail);
    const [carregando, setCarregando] = useState(1);
    const [excluido, setExcluido] = useState(0);

    useEffect(() => {
        if (carregando) {
            firebase.firestore().collection('carregamentos').doc(props.match.params.id).get().then(resultado => {
                setCarregamento(resultado.data())
                firebase.firestore().collection('carregamentos').doc(props.match.params.id).update('visualizacoes', resultado.data().visualizacoes + 1)

                firebase.storage().ref(`carregamentos/${resultado.data().foto1}`).getDownloadURL().then(url => {
                    setFoto1(url)
                });
                firebase.storage().ref(`carregamentos/${resultado.data().foto2}`).getDownloadURL().then(url => {
                    setFoto2(url)
                });
                firebase.storage().ref(`carregamentos/${resultado.data().foto3}`).getDownloadURL().then(url => {
                    setFoto3(url)
                });
                firebase.storage().ref(`carregamentos/${resultado.data().foto4}`).getDownloadURL().then(url => {
                    setFoto4(url)
                });
                firebase.storage().ref(`carregamentos/${resultado.data().foto5}`).getDownloadURL().then(url => {
                    setFoto5(url)
                });
                firebase.storage().ref(`carregamentos/${resultado.data().foto6}`).getDownloadURL().then(url => {
                    setFoto6(url)
                });
                firebase.storage().ref(`carregamentos/${resultado.data().foto7}`).getDownloadURL().then(url => {
                    setFoto7(url)
                });
                firebase.storage().ref(`carregamentos/${resultado.data().foto8}`).getDownloadURL().then(url => {
                    setFoto8(url)
                });
                firebase.storage().ref(`carregamentos/${resultado.data().foto9}`).getDownloadURL().then(url => {
                    setFoto9(url)
                });
                firebase.storage().ref(`carregamentos/${resultado.data().foto10}`).getDownloadURL().then(url => {
                    setFoto10(url)
                });
                firebase.storage().ref(`carregamentos/${resultado.data().foto11}`).getDownloadURL().then(url => {
                    setFoto11(url)
                });
                firebase.storage().ref(`carregamentos/${resultado.data().foto12}`).getDownloadURL().then(url => {
                    setFoto12(url)
                });
                firebase.storage().ref(`carregamentos/${resultado.data().foto13}`).getDownloadURL().then(url => {
                    setFoto13(url)
                });
                firebase.storage().ref(`carregamentos/${resultado.data().foto14}`).getDownloadURL().then(url => {
                    setFoto14(url)
                });
                firebase.storage().ref(`carregamentos/${resultado.data().foto15}`).getDownloadURL().then(url => {
                    setFoto15(url)
                });
                setCarregando(0)
            });
        } else {
            firebase.storage().ref(`carregamentos/${carregamento.foto1}`).getDownloadURL().then(url => {
                setFoto1(url)
            });
            firebase.storage().ref(`carregamentos/${carregamento.foto2}`).getDownloadURL().then(url => {
                setFoto2(url)
            });
            firebase.storage().ref(`carregamentos/${carregamento.foto3}`).getDownloadURL().then(url => {
                setFoto3(url)
            });
            firebase.storage().ref(`carregamentos/${carregamento.foto4}`).getDownloadURL().then(url => {
                setFoto4(url)
            });
            firebase.storage().ref(`carregamentos/${carregamento.foto5}`).getDownloadURL().then(url => {
                setFoto5(url)
            });
            firebase.storage().ref(`carregamentos/${carregamento.foto6}`).getDownloadURL().then(url => {
                setFoto6(url)
            });
            firebase.storage().ref(`carregamentos/${carregamento.foto7}`).getDownloadURL().then(url => {
                setFoto7(url)
            });
            firebase.storage().ref(`carregamentos/${carregamento.foto8}`).getDownloadURL().then(url => {
                setFoto8(url)
            });
            firebase.storage().ref(`carregamentos/${carregamento.foto9}`).getDownloadURL().then(url => {
                setFoto9(url)
            });
            firebase.storage().ref(`carregamentos/${carregamento.foto10}`).getDownloadURL().then(url => {
                setFoto10(url)
            });
            firebase.storage().ref(`carregamentos/${carregamento.foto11}`).getDownloadURL().then(url => {
                setFoto11(url)
            });
            firebase.storage().ref(`carregamentos/${carregamento.foto12}`).getDownloadURL().then(url => {
                setFoto12(url)
            });
            firebase.storage().ref(`carregamentos/${carregamento.foto13}`).getDownloadURL().then(url => {
                setFoto13(url)
            });
            firebase.storage().ref(`carregamentos/${carregamento.foto14}`).getDownloadURL().then(url => {
                setFoto14(url)
            });
            firebase.storage().ref(`carregamentos/${carregamento.foto15}`).getDownloadURL().then(url => {
                setFoto15(url)
            });
            setCarregando(0)
        }
    }, [carregamento.foto1, carregamento.foto2, carregamento.foto3, carregamento.foto4, carregamento.foto5, 
        carregando, props.match.params.id, carregamento.foto10, carregamento.foto11, carregamento.foto12,
        carregamento.foto13, carregamento.foto14, carregamento.foto15, carregamento.foto6, carregamento.foto7,
        carregamento.foto8, carregamento.foto9
    ])

    function remover(){
        firebase.firestore().collection('carregamentos').doc(props.match.params.id).delete().then(() => {
            setExcluido(1)
        })
    }

    return (
        <>
            <Navbar />

            {excluido ? <Redirect to='/' /> : null}

            {carregando ? <div className="row detalhes mt-5"><div class="spinner-border text-danger mx-auto" role="status"></div></div>
                :
                <div className="body-content mx-auto form col-md-10 col-sm-12 p-3">
                    <div id="demo" className="mx-auto col-md-10 col-sm-12 box-info p-3 carousel" >

                        <ul className="carousel-indicators">
                            <li data-target="#demo" data-slide-to="0" class="active"></li>
                            <li data-target="#demo" data-slide-to="1"></li>
                            <li data-target="#demo" data-slide-to="2"></li>
                        </ul>

                        <div className="mx-auto carousel-inner">
                        { urlFoto1 ?
                                <div className="mx-auto carousel-item active">
                                <img src={urlFoto1} alt="Foto 1" />
                            </div>
                            : null}
                            { urlFoto2 ?
                                <div className="mx-auto carousel-item">
                                <img src={urlFoto2} alt="Foto 2" />
                            </div>
                            : null}
                            { urlFoto3 ?
                                <div className="mx-auto carousel-item">
                                <img src={urlFoto3} alt="Foto 3" />
                            </div>
                            : null}
                            { urlFoto4 ?
                                <div className="mx-auto carousel-item">
                                <img src={urlFoto4} alt="Foto 4" />
                            </div>
                            : null}
                            { urlFoto5 ?
                                <div className="mx-auto carousel-item">
                                <img src={urlFoto5} alt="Foto 5" />
                            </div>
                            : null}
                            { urlFoto6 ?
                                <div className="mx-auto carousel-item">
                                <img src={urlFoto6} alt="Foto 6" />
                            </div>
                            : null}
                            { urlFoto7 ?
                                <div className="mx-auto carousel-item">
                                <img src={urlFoto7} alt="Foto 7" />
                            </div>
                            : null}
                            { urlFoto8 ?
                                <div className="mx-auto carousel-item">
                                <img src={urlFoto8} alt="Foto 8" />
                            </div>
                            : null}
                            { urlFoto9 ?
                                <div className="mx-auto carousel-item">
                                <img src={urlFoto9} alt="Foto 9" />
                            </div>
                            : null}
                            { urlFoto10 ?
                                <div className="mx-auto carousel-item">
                                <img src={urlFoto10} alt="Foto 10" />
                            </div>
                            : null}
                            { urlFoto11 ?
                                <div className="mx-auto carousel-item">
                                <img src={urlFoto11} alt="Foto 11" />
                            </div>
                            : null}
                            { urlFoto12 ?
                                <div className="mx-auto carousel-item">
                                <img src={urlFoto12} alt="Foto 12" />
                            </div>
                            : null}
                            { urlFoto13 ?
                                <div className="mx-auto carousel-item">
                                <img src={urlFoto13} alt="Foto 13" />
                            </div>
                            : null}
                            { urlFoto14 ?
                                <div className="mx-auto carousel-item">
                                <img src={urlFoto14} alt="Foto 14" />
                            </div>
                            : null}
                            { urlFoto15 ?
                                <div className="mx-auto carousel-item">
                                <img src={urlFoto15} alt="Foto 15" />
                            </div>
                            : null}
                        </div>
                        <a class="carousel-control-prev" href="#demo" data-slide="prev">
                            <span class="carousel-control-prev-icon"></span>
                        </a>
                        <a class="carousel-control-next" href="#demo" data-slide="next">
                            <span class="carousel-control-next-icon"></span>
                        </a>
                    </div>

                    <div className="container-fluid">
                        <div>
                            <div className="row">
                                <h3 className="mx-auto mt-5 titulo"><strong>{carregamento.cliente}</strong></h3>
                            </div>

                            <div className="row mt-5 d-flex justify-content-around ">
                                <div className="col-md-3 col-sm-12 box-info p-3 my-2">
                                    <i className="fas fa-ticket-alt fa-2x"></i>
                                    <h5><strong>Responsavel</strong></h5>
                                    <span className="mt-3">{carregamento.responsavel}</span>
                                </div>

                                <div className="col-md-3 col-sm-12 box-info p-3 my-2">
                                    <i className="fas fa-calendar-alt fa-2x"></i>
                                    <h5><strong>Data</strong></h5>
                                    <span className="mt-3">{carregamento.data}</span>
                                </div>

                                <div className="col-md-3 col-sm-12 box-info p-3 my-2">
                                    <i className="fas fa-clock fa-2x"></i>
                                    <h5><strong>Numero da Nota</strong></h5>
                                    <span className="mt-3">{carregamento.nota}</span>
                                </div>
                            </div>

                            <div className="row box-detalhes mt-5">
                                <div className="col-12 text-center">
                                    <h5><strong>Detalhes do Carregamento</strong></h5>
                                </div>
                                <div className="col-12 text-center">
                                    <p>{carregamento.detalhes}</p>
                                </div>
                            </div>
                            {
                                usuarioLogado === carregamento.usuario ?
                                <>
                                <div className="col-12 semDeco centro">
                                    <Link className="col-sm-12 semDeco col-md-6" 
                                    to={`/editarcarregamento/${props.match.params.id}`}>
                                        <button type="button"
                                    className="text-white semDeco btn btn-lg btn-block mt-3 mb-1 btn-success">
                                    <i className="fas text-white fa-pen-square fa-1x mr-1"></i>
                                          Editar</button></Link>
                                    <button  type="button" onClick={remover} 
                                    className="col-sm-12 text-white col-md-6 btn btn-lg btn-block mt-3 mb-1 btn-danger">
                                    <i class="fas fa-trash fa-1x mr-1"></i>
                                        Remover</button>
                                </div>
                                </>
                                    : ''
                            }
                        </div>
                    </div>
            <table hidden  id="table-to-xls" class="table">
                <tr>
                    <td>Foto 1</td>
                    <td>Foto 2</td>
                    <td>Foto 3</td>
                    <td>Foto 4</td>
                    <td>Foto 5</td>
                </tr>
                <tr>
                    <td>{urlFoto1 ? urlFoto1 : "Sem Imagem"}</td>
                    <td>{urlFoto2 ? urlFoto2 : "Sem Imagem"}</td>
                    <td>{urlFoto3 ? urlFoto3 : "Sem Imagem"}</td>
                    <td>{urlFoto4 ? urlFoto4 : "Sem Imagem"}</td>
                    <td>{urlFoto5 ? urlFoto5 : "Sem Imagem"}</td>
                </tr>
                <tr>
                    <td>Foto 6</td>
                    <td>Foto 7</td>
                    <td>Foto 8</td>
                    <td>Foto 9</td>
                    <td>Foto 10</td>
                </tr>
                <tr>
                    <td>{urlFoto6 ? urlFoto6 : "Sem Imagem"}</td>
                    <td>{urlFoto7 ? urlFoto7 : "Sem Imagem"}</td>
                    <td>{urlFoto8 ? urlFoto8 : "Sem Imagem"}</td>
                    <td>{urlFoto9 ? urlFoto9 : "Sem Imagem"}</td>
                    <td>{urlFoto10 ? urlFoto10 : "Sem Imagem"}</td>
                </tr>
                <tr>
                    <td>Foto 11</td>
                    <td>Foto 12</td>
                    <td>Foto 13</td>
                    <td>Foto 14</td>
                    <td>Foto 15</td>
                </tr>
                <tr>
                    <td>{urlFoto11 ? urlFoto11 : "Sem Imagem"}</td>
                    <td>{urlFoto12 ? urlFoto12 : "Sem Imagem"}</td>
                    <td>{urlFoto13 ? urlFoto13 : "Sem Imagem"}</td>
                    <td>{urlFoto14 ? urlFoto14 : "Sem Imagem"}</td>
                    <td>{urlFoto15 ? urlFoto15 : "Sem Imagem"}</td>
                </tr>
            <tr>
                    <td><strong>CLIENTE</strong></td>
                    <td><strong>RESPONSAVEL</strong></td>
                    <td><strong>DATA</strong></td>
                    <td><strong>NOTA</strong></td>
                    <td><strong>DETALHES</strong></td>
                </tr>
                <tr>
                    <td><strong>{carregamento.cliente}</strong></td>
                    <td><strong>{carregamento.responsavel}</strong></td>
                    <td><strong>{carregamento.data}</strong></td>
                    <td><strong>{carregamento.nota}</strong></td>
                    <td><strong>{carregamento.detalhes}</strong></td>
                </tr>
            </table>
            <ReactToExcel
                className="col-md-12 col-sm-12 box-info  p-3 my-2" 
                table="table-to-xls" 
                filename={carregamento.cliente + "-" + carregamento.nota + "-" + carregamento.data}
                sheet={carregamento.cliente + "-" + carregamento.nota + "-" + carregamento.data} buttonText="EXPORTAR PARA EXCEL"
            />
        </div>

}
<Footer />
        </>
    )
}

export default CarregamentoDetalhes;