import React, { useState, useEffect } from 'react';
import './ver-carregamentos.css';
import Navbar from '../../components/navbar/';
import Footer from '../../components/footer/';
import { useSelector } from 'react-redux';
import firebase from '../../config/firebase';
import EventoCarregamento from '../../components/evento-card-carregamento/'

function VisualizarEventosCarregamento({ match }) {

    const [pesquisa, setPesquisa] = useState('');
    const [carregamentos, setCarregamentos] = useState([]);
    const [carregando , setCarregando] = useState(1)
    let listaCarregamentos = [];
    const usuarioEmail = useSelector(state => state.usuarioEmail);

    useEffect(() => {

        if (match.params.parametro) {
            firebase.firestore().collection('carregamentos').where('usuario', '==', usuarioEmail).get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    if (doc.data().nota.indexOf(pesquisa) >= 0) {
                        listaCarregamentos.push({
                            id: doc.id,
                            ...doc.data()
                        })
                    }
                })

                setCarregamentos(listaCarregamentos);
                setCarregando(0)
            });

        } else {
            firebase.firestore().collection('carregamentos').get().then(async (resultado) => {
                await resultado.docs.forEach(doc => {
                    if (doc.data().nota.indexOf(pesquisa) >= 0) {
                        listaCarregamentos.push({
                            id: doc.id,
                            ...doc.data()
                        })
                    }
                })

                setCarregamentos(listaCarregamentos);
                setCarregando(0)
            });
        }
    }, [listaCarregamentos, match.params.parametro, pesquisa, usuarioEmail]);
    
    return (
        <>
            <Navbar />
            {
                carregando ? <div className="row mt-5"><div class="spinner-border text-danger mx-auto" role="status"></div></div>
                :
            <div className="body-content col-sm-12 col-md-8 mx-auto datascreen">
            <div className="row p-3 mx-auto">
                <input className="search col-sm-8 mx-auto form-control text-center" 
                 onChange={(e) => setPesquisa(e.target.value)} type="text"
                placeholder="Pesquisar Carregamento pelo ttulo..." />
            </div>

            <div className="row exibicao mx-2 text-center">
                {carregamentos.map(item => <EventoCarregamento key={item.id} id={item.id} img={item.foto1}
                    nota={item.nota} cliente={item.cliente} data={item.data} />)}
            </div>
            </div>
            }
            <Footer />
        </>
    )
}

export default VisualizarEventosCarregamento;