import React from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import { store, persistor } from '../src/store/';
import { Provider } from 'react-redux';
import { PersistGate} from 'redux-persist/integration/react';

/*PGINAS*/
import Login from './view/login/';
import NovoUsuario from './view/usuario-novo/';
import Home from './view/home/';
import VerCarregamentos from './components/visualizar-eventos-carregamento/'
import VerRecebimentos from './components/visualizar-eventos-recebimento/'
import VerQualidade from './components/visualizar-eventos-qualidade/'
import VerOrcamentos from './components/visualizar-eventos-orcamento/'
import UsuarioRecuperarSenha from './view/usuario-recuperar-senha/';
import Recebimentocadastro from '../src/view/recebimento-cadastro/'
import CarregamentoCadastro from '../src/view/carregamento-cadastro/'
import QualidadeCadastro from '../src/view/qualidade-cadastro/'
import RecebimentoDetalhes from './view/recebimento-detalhes/'
import CarregamentoDetalhes from './view/carregamento-detalhes/'
import OrcamentoDetalhes from './view/orcamento-detalhes/'
import QualidadeDetalhes from './view/qualidade-detalhes/'
import FuncionarioDetalhes from './view/funcionario-detalhes/'
import ClienteDetalhes from './view/cliente-detalhes/'
import FornecedorDetalhes from './view/fornecedor-detalhes/'
import OrcamentoCadastro from '../src/view/orcamento-cadastro'
import FuncionarioCadastro from '../src/view/funcionario-cadastro'
import ClienteCadastro from './view/cliente-cadastro';
import FornecedorCadastro from './view/fornecedor-cadastro';
import MpCadastro from './view/mp-cadastro';
import VerFuncionarios from './components/visualizar-eventos-funcionarios';
import VerClientes from './components/visualizar-eventos-clientes';
import VerFornecedores from './components/visualizar-eventos-fornecedores';
import VisualizarEventosMp from './components/visualizar-eventos-mp/';
import VisualizarEventosSetor from './components/visualizar-eventos-setor/';
import MpDetalhes from './view/mp-detalhes';
import ServicoCadastro from './view/servico-cadastro';
import VisualizarEventosServicos from './components/visualizar-eventos-servico';
import ServicoDetalhes from './view/servico-detalhes';
import AdminPage from './view/admin-cadastro-key';
import SetorCadastro from './view/setor-cadastro';
import TipoNcfCadastro from './view/tipo_ncf-cadastro';
import VisualizarEventosTiposNcf from './components/visualizar-eventos-tipo_ncf';
import SetorDetalhes from './view/setor-detalhes';
import TipoNcfDetalhes from './view/tipo_ncf-detalhes';
import MaquinaDetalhes from './view/maquina-detalhes';
import PcpDetalhes from './view/pcp-detalhes';
import PcpCadastro from './view/pcp-cadastro';
import MaquinaCadastro from './view/maquina-cadastro';
import VisualizarEventosMaquinas from './components/visualizar-eventos-maquina';
import VisualizarEventosB6520 from './components/visualizar-eventos-b6520';
import VisualizarEventosB4025 from './components/visualizar-eventos-b4025';
import PcpInsights from './view/pcp-insights';
import VisualizarEventosDurma from './components/visualizar-eventos-durma';
import VisualizarEventosXcite from './components/visualizar-eventos-xcite';
import VisualizarEventosXpert from './components/visualizar-eventos-xpert';

function App() {
  return (
    <Provider store={store}>
      <PersistGate loading={null} persistor={persistor}>
      <Router>
        <Route exact path='/' component={Home} />
        <Route exact path='/vercarregamentos' component={VerCarregamentos} />
        <Route path='/carregamentos/:parametro' component={VerCarregamentos} />
        <Route exact path='/verrecebimentos' component={VerRecebimentos} />
        <Route path='/recebimentos/:parametro' component={VerRecebimentos} />
        <Route exact path='/verqualidade' component={VerQualidade} />
        <Route path='/qualidade/:parametro' component={VerQualidade} />
        <Route exact path='/verorcamentos' component={VerOrcamentos} />
        <Route path='/orcamento/:parametro' component={VerOrcamentos} />
        <Route exact path='/verfuncionarios' component={VerFuncionarios} />
        <Route exact path='/verclientes' component={VerClientes} />
        <Route exact path='/verfornecedores' component={VerFornecedores} />
        <Route exact path='/novousuario' component={NovoUsuario} />
        <Route exact path='/login' component={Login} />
        <Route exact path='/usuariorecuperarsenha' component={UsuarioRecuperarSenha} />
        <Route exact path='/recebimentocadastro' component={Recebimentocadastro} />
        <Route exact path='/carregamentocadastro' component={CarregamentoCadastro} />
        <Route exact path='/qualidadecadastro' component={QualidadeCadastro} />
        <Route exact path='/orcamentocadastro' component={OrcamentoCadastro} />
        <Route path='/recebimentodetalhes/:id' component={RecebimentoDetalhes} />
        <Route path='/carregamentodetalhes/:id' component={CarregamentoDetalhes} />
        <Route path='/qualidadedetalhes/:id' component={QualidadeDetalhes} />
        <Route path='/orcamentodetalhes/:id' component={OrcamentoDetalhes} />
        <Route path='/funcionariodetalhes/:id' component={FuncionarioDetalhes} />
        <Route path='/clientedetalhes/:id' component={ClienteDetalhes} />
        <Route path='/fornecedordetalhes/:id' component={FornecedorDetalhes} />
        <Route path='/mpdetalhes/:id' component={MpDetalhes} />
        <Route path='/servicodetalhes/:id' component={ServicoDetalhes} />
        <Route path='/setordetalhes/:id' component={SetorDetalhes} />
        <Route path='/tiponcfdetalhes/:id' component={TipoNcfDetalhes} />
        <Route path='/maquinadetalhes/:id' component={MaquinaDetalhes} />
        <Route path='/pcpdetalhes/:id/:mq' component={PcpDetalhes} />
        <Route path='/editarrecebimento/:id' component={Recebimentocadastro} />
        <Route path='/editarcarregamento/:id' component={CarregamentoCadastro} />
        <Route path='/editarqualidade/:id' component={QualidadeCadastro} />
        <Route path='/editarfuncionarios/:id' component={FuncionarioCadastro} />
        <Route path='/editarclientes/:id' component={ClienteCadastro} />
        <Route path='/editarfornecedores/:id' component={FornecedorCadastro} />
        <Route path='/editarmp/:id' component={MpCadastro} />
        <Route path='/editarservico/:id' component={ServicoCadastro} />
        <Route path='/funcionariocadastro' component={FuncionarioCadastro} />
        <Route path='/clientecadastro' component={ClienteCadastro} />
        <Route path='/fornecedorcadastro' component={FornecedorCadastro} />
        <Route path='/mpcadastro' component={MpCadastro} />
        <Route path='/servicocadastro' component={ServicoCadastro} />
        <Route path='/setorcadastro' component={SetorCadastro} />
        <Route path='/tiponcfcadastro' component={TipoNcfCadastro} />
        <Route path='/pcpcadastro' component={PcpCadastro} />
        <Route path='/maquinacadastro' component={MaquinaCadastro} />
        <Route path='/vermp' component={VisualizarEventosMp} />
        <Route path='/versetores' component={VisualizarEventosSetor} />
        <Route path='/vertiposncf' component={VisualizarEventosTiposNcf} />
        <Route path='/verservicos' component={VisualizarEventosServicos} />
        <Route path='/vermaquinas' component={VisualizarEventosMaquinas} />
        <Route path='/verbysprint6520' component={VisualizarEventosB6520} />
        <Route path='/verbysprint4025' component={VisualizarEventosB4025} />
        <Route path='/verdurma' component={VisualizarEventosDurma} />
        <Route path='/verxcite' component={VisualizarEventosXcite} />
        <Route path='/verxpert' component={VisualizarEventosXpert} />
        <Route path='/verpcpinsights' component={PcpInsights} />
        <Route path='/adminpage' component={AdminPage} />
        <Route path='/editarsetor/:id' component={SetorCadastro} />
        <Route path='/editartiponcf/:id' component={TipoNcfCadastro} />
        <Route path='/editarmaquina/:id' component={MaquinaCadastro} />
        <Route path='/editarpcp/:id/:mq' component={PcpCadastro} />
      </Router>
      </PersistGate>
    </Provider>
  );
}

export default App;
