import React, { useState, useEffect } from 'react';
import './pcp.css';
import firebase from '../../config/firebase';
import Navbar from '../../components/navbar';
import Footer from '../../components/footer';
import ReactToExcel from 'react-html-table-to-excel';

function PcpInsights(props) {
    const [resultados, setResultados] = useState([]);
    const [buscar, setBuscar] = useState(0)
    const [start, setData1] = useState(0)
    const [end, setData2] = useState(0)
    const [maquina, setMaquina] = useState('')

    let Lista = [];

    useEffect(() => {
        if (buscar === 1) {
            if (maquina === '1') {
                let ref = firebase.firestore().collection('B6520');
                setResultados([])
                ref.where('dataMili', '>=', Date.parse(start)).where('dataMili', '<=', Date.parse(end))
                    .get().then(async (resultado) => {
                        await resultado.docs.forEach(doc => {
                            Lista.push({
                                id: doc.id,
                                ...doc.data()
                            })
                        })
                        setResultados(Lista)
                        console.log(resultados)
                        setBuscar(0)
                    }
                    )
            }
            else if (maquina === '2') {
                let ref = firebase.firestore().collection('B4025');
                setResultados([])
                ref.where('dataMili', '>=', Date.parse(start)).where('dataMili', '<=', Date.parse(end))
                    .get().then(async (resultado) => {
                        await resultado.docs.forEach(doc => {
                            Lista.push({
                                id: doc.id,
                                ...doc.data()
                            })
                        })
                        setResultados(Lista)
                        console.log(resultados)
                        setBuscar(0)
                    }
                    )
            }
            else if (maquina === '3') {
                let ref = firebase.firestore().collection('Xcite');
                setResultados([])
                ref.where('dataMili', '>=', Date.parse(start)).where('dataMili', '<=', Date.parse(end))
                    .get().then(async (resultado) => {
                        await resultado.docs.forEach(doc => {
                            Lista.push({
                                id: doc.id,
                                ...doc.data()
                            })
                        })
                        setResultados(Lista)
                        console.log(resultados)
                        setBuscar(0)
                    }
                    )
            }
            else if (maquina === '4') {
                let ref = firebase.firestore().collection('Xpert');
                setResultados([])
                ref.where('dataMili', '>=', Date.parse(start)).where('dataMili', '<=', Date.parse(end))
                    .get().then(async (resultado) => {
                        await resultado.docs.forEach(doc => {
                            Lista.push({
                                id: doc.id,
                                ...doc.data()
                            })
                        })
                        setResultados(Lista)
                        console.log(resultados)
                        setBuscar(0)
                    }
                    )
            }
            else if (maquina === '5') {
                let ref = firebase.firestore().collection('durma');
                setResultados([])
                ref.where('dataMili', '>=', Date.parse(start)).where('dataMili', '<=', Date.parse(end))
                    .get().then(async (resultado) => {
                        await resultado.docs.forEach(doc => {
                            Lista.push({
                                id: doc.id,
                                ...doc.data()
                            })
                        })
                        setResultados(Lista)
                        console.log(resultados)
                        setBuscar(0)
                    }
                    )
            }
            else {
                alert("ERRO AO BUSCAR NO BANCO DE DADOS")
                setBuscar(0)
            }
        }
    }, [Lista, buscar, end, maquina, resultados, start])

    function buscarPcp() {
        setBuscar(1)
    }
    // eslint-disable-next-line 
    function dateToBR(data) {
        return data.split('-').reverse().join('/');
    }
    return (
        <>
            <Navbar />

            <div className="body-content mx-auto form col-md-10 col-sm-12 p-3">
                <div className="container-fluid">
                    <div>
                        <div className="p-3 text-center">
                            <h3 className="mx-auto mt-5 titulo"><strong>Pcp Insights</strong></h3></div>
                        <div className="row col-12 mt-3 p-3">
                            <div className="form-group f-component col-sm-12 col-md-4">
                                <label>Data Inicio</label>
                                <input onChange={(e) => setData1(e.target.value)}
                                    type="date" className="form-control" />
                            </div>
                            <div className="form-group f-component col-sm-12 col-md-4">
                                <label>Data Fim</label>
                                <input onChange={(e) => setData2(e.target.value)}
                                    type="date" className="form-control" />
                            </div>
                            <div className="form-group col-sm-12 col-md-2 f-component">
                                <label>Maquina</label>
                                <select onChange={(e) => setMaquina(e.target.value)}
                                    className="form-control">
                                    <option selected value="1">Selecione a maquina</option>
                                    <option value="1">Bysprint 6520</option>
                                    <option value="2">Bystar 4025</option>
                                    <option value="3">Xcite</option>
                                    <option value="4">Xpert</option>
                                    <option value="5">Dobradeira Durma</option>
                                </select>
                            </div>
                            <div className="row mt-1 col-sm-12 col-md-2 d-flex ">
                                <button onClick={buscarPcp} className="btn btn-primary">Buscar</button>
                            </ div>
                        </div>
                    </div>
                    <table id="table-to-xls" className="font" class="table">
                        <thead class="thead-dark">
                            <tr>
                                <th scope="col">Maquina</th>
                                <th scope="col">Data</th>
                                <th scope="col">Horas a produzir</th>
                                <th scope="col">Horas produzidas</th>
                                <th scope="col">% Aproveitamento</th>
                            </tr>
                        </thead>
                        {resultados.map(list =>
                            <tr key={list.id}>
                                <td>{list.maquina}</td>
                                <td>{list.data}</td>
                                <td>{list.horasaproduzir}</td>
                                <td>{list.horasproduzidas}</td>
                                <td>{list.horasproduzidas / list.horasaproduzir * 100}</td>
                            </tr>)}
                        <thead class="thead-dark">
                            <tr>
                                <th colSpan="2" scope="col">Total de horas planejadas</th>
                                <th colSpan="2" scope="col">Total de horas de produo executadas</th>
                                <th scope="col">Aproveitamento total</th>
                            </tr>
                        </thead>
                        <tr>
                            <td colSpan="2">
                                {resultados.length > 0 ?
                                    resultados.reduce((total, atual) => { return parseInt(total) + parseInt(atual.horasaproduzir) }, 0) : null}</td>
                            <td colSpan="2">
                                {resultados.length > 0 ?
                                    resultados.reduce((total, atual) => { return parseInt(total) + parseInt(atual.horasproduzidas) }, 0) : null}</td>
                            <td>{resultados.length > 0 ?
                                (resultados.reduce((total, atual) => { return parseInt(total) + parseInt(atual.horasproduzidas) }, 0) /
                                    resultados.reduce((total, atual) => { return parseInt(total) + parseInt(atual.horasaproduzir) }, 0) * 100).toFixed(2) + '%'
                                : null}</td>
                        </tr>
                    </table>
                </div>
                <ReactToExcel
                className="col-md-12 col-sm-12 box-info  p-3 mb-2 " table="table-to-xls"
                filename={`Relatorio de produo inicio:${start} --- fim: ${end}`}
                sheet={`Relatorio de produo inicio:${start} --- fim: ${end}`}
                buttonText="EXPORTAR PARA EXCEL"
            />
            </div>
            <Footer />
        </>
    )
}

export default PcpInsights;